import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { MatDialog } from '@angular/material';
import { EeChequesPDialogComponent } from './../../../components/common/ee-cheques-p-dialog/ee-cheques-p-dialog.component';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { APPCONFIG } from './../../config';
import { CategoriaProvider } from './../../../providers/categoria-provider';
import { EEValidators } from './../../../utils/eevalidators';
import { EEMasks } from './../../../utils/eemasks';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { SubCategoriaProvider } from './../../../providers/subcategoria-provider';
import { FormBuilder, FormGroup, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { Global } from "../../../variables/Global";
var _ = require('lodash');
@Component({
    selector: 'gasto',
    template: require('./gasto.component.html')
})
export class GastoComponent {
    private currencyMask = EEMasks.eecurrency;
    private chequeMask = EEMasks.chequeMask;
    private dateMask = EEMasks.date;
    private startViewDate;
    private existingChequeID: number;
    private existingChequeMonto: number;
    private subCategorias;
    private nombreCategoria;
    private subCategoriaSelected;
    private categoria: string;
    private hideDescription: boolean = false;
    @ViewChild(FormGroupDirective) myForm;
    private props;
    private sub: any;
    private enableCheque;
    private bancoDefault = 'Galicia';
    datares: any;
    gastoForm: FormGroup;
    formSubmitted = false;
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private categoriaProvider: CategoriaProvider,
        private subCategoriaProvider: SubCategoriaProvider,
        private movimientosProvider: MovimientosProvider,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private dialog: MatDialog,
        private eetoast: EEToast
    ) {
        this.sub = this.route.params.subscribe(params => {
            this.categoria = params['categoria'];
            if (this.categoria === "6" || this.categoria === "3") {
                this.hideDescription = true;
            } else {
                this.hideDescription = false;
            }
            if (this.gastoForm) this.OnCancelForm();
            this.getSubCategorias()
        });

    }

    ngOnInit() {
        let initialDate = new Date();
        this.startViewDate = EEGerCommons.limit5Days();
        this.gastoForm = this.formBuilder.group({
            subcategoriaID: this.formBuilder.control(this.subCategoriaSelected, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(initialDate, [Validators.required]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
        });

        this.gastoForm.controls['subcategoriaID']
            .valueChanges
            .subscribe((value) => {
                if (value) {
                    console.log("sub id", value);
                    if (value === 18 || value === 13) {
                        this.hideDescription = true;
                    } else {
                        this.hideDescription = false;
                    }
                    this.removeControls();
                    this.getSubCategoriaData(value);
                }
            });
    }

    eventsCheque() {
        this.gastoForm.controls['medioPagoID']
            .valueChanges
            .subscribe((value) => {
                if (value === 3) {
                    this.enableChequeInputs();
                } else {
                    if (this.gastoForm.controls['monto'].disabled) this.gastoForm.controls['monto'].enable();
                    this.removeChequeInputs();
                    this.enableCheque = false;
                    this.existingChequeID = null;
                }
            });
    }

    buildForm() {
        if (this.props.medioPagos) {
            if (this.categoria === "6") {
                this.props.medioPagos = null;
            } else {
                this.gastoForm.addControl('medioPagoID', new FormControl(null, Validators.required));
                this.eventsCheque();
            }
        }
        if (this.props.residencias) {
            this.gastoForm.addControl('residenciaID', new FormControl(null, Validators.required));
        }
        if (this.props.enfermeros) {
            this.gastoForm.addControl('enfermeroID', new FormControl(null, Validators.required));
        }
        if (this.props.cocineros) {
            this.gastoForm.addControl('cocineroID', new FormControl(null, Validators.required));
        }
        if (this.props.personales) {
            this.gastoForm.addControl('personalID', new FormControl(null, Validators.required));
        }
        if (this.props.mpCocinas) {
            this.gastoForm.addControl('mpCocinaID', new FormControl(null, Validators.required));
        }
        if (this.props.servicios) {
            this.gastoForm.addControl('servicioID', new FormControl(null, Validators.required));
        }
        if (this.props.residentes) {
            this.datares = this.props.residentes;
            if (this.datares && this.datares && this.datares.length > 0) {
                this.datares.forEach(element => {
                    element['fname'] = element.nombre.split(' ')[0];
                    element['lname'] = element.nombre.split(' ')[1];
                });
                this.datares = _.orderBy(this.datares, ['lname'], ['asc']);
            }
            this.props.residentes = this.datares;
            this.gastoForm.addControl('residenteID', new FormControl(null, Validators.required));
        }
        if (this.props.convenios) {
            this.gastoForm.addControl('convenioID', new FormControl(null, Validators.required));
        }

        if (this.props.inputs) {
            this.props.inputs.forEach(element => {
                if (element.isOptional) {
                    this.gastoForm.addControl(element.nombre, new FormControl(null));
                } else {
                    this.gastoForm.addControl(element.nombre, new FormControl(null, [Validators.required]))
                }
            });
        }
        this.gastoForm.updateValueAndValidity();
    }

    getChequesToPay() {
        if (!this.existingChequeID) {
            let dialogRef = this.dialog.open(EeChequesPDialogComponent, {
                hasBackdrop: true
            });
            dialogRef.afterClosed().subscribe(result => {
                if (result) {
                    this.existingChequeID = result.id;
                    this.existingChequeMonto = result.monto;
                    this.gastoForm.controls['nroCheque'].setValue(result.numero);
                    this.gastoForm.controls['nroCheque'].disable();
                    this.gastoForm.controls['emitidoCheque'].setValue(result.fecha);
                    this.gastoForm.controls['emitidoCheque'].disable();
                    this.gastoForm.controls['pagoCheque'].disable();
                    this.gastoForm.controls['banco'].setValue(result.banco);
                    this.gastoForm.controls['banco'].disable();
                    this.gastoForm.controls['monto'].setValue(this.existingChequeMonto);
                    this.gastoForm.controls['monto'].disable();
                }
            });
        }
    }
    enableChequeInputs() {
        this.enableCheque = true;
        let initialDate = new Date();
        this.gastoForm.addControl('nroCheque', new FormControl(null, Validators.required));
        this.gastoForm.addControl('banco', new FormControl('Galicia', Validators.required));
        this.gastoForm.addControl('emitidoCheque', new FormControl(initialDate, Validators.required));
        this.gastoForm.addControl('pagoCheque', new FormControl(null, Validators.required));
        this.gastoForm.addControl('destinoCheque', new FormControl(null, Validators.required));
    }

    removeChequeInputs() {
        this.gastoForm.removeControl('nroCheque');
        this.gastoForm.removeControl('banco');
        this.gastoForm.removeControl('emitidoCheque');
        this.gastoForm.removeControl('pagoCheque');
        this.gastoForm.removeControl('destinoCheque');
    }

    getSubCategorias() {
        this.eeloading.open();
        this.categoriaProvider.getSubCategorias(this.categoria)
            .subscribe(
            (data: any) => {
                this.subCategorias = data.subcategorias;
                this.nombreCategoria = data.nombre;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    getSubCategoriaData(subCategoriaID) {
        this.eeloading.open();
        this.subCategoriaProvider.getSubCategoriaData(subCategoriaID, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.props = data;
                this.buildForm();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
                //console.log(error);
            })
    }

    onResetForm() {
        //this.gastoForm.reset();
        this.formSubmitted = false;
    }

    onSubmit() {
        this.formSubmitted = true;
        if (!this.gastoForm.valid) return;
        this.eeloading.open();
        let formatata = this.gastoForm.getRawValue();
        formatata.tipoMovimiento = 1;
        formatata.residenciaID = APPCONFIG.brand.id;
        formatata.monto = formatata.monto.replace('$ ', '');
        if (formatata.monto.indexOf('.') > -1) {
            formatata.monto = formatata.monto.replace('.', '');
        }
        if (formatata.medioPagoID && formatata.medioPagoID === 3) {
            formatata.cheque = {
                numero: formatata.nroCheque,
                banco: formatata.banco,
                fechaEmision: formatata.emitidoCheque,
                fechaPago: formatata.pagoCheque,
                tipo: this.existingChequeID ? 'REC' : 'EMI',
                chequeID: this.existingChequeID,
                residenciaID: formatata.residenciaID,
                destino: formatata.destinoCheque,
                estado: (this.existingChequeID) ? 'procesado' : 'registrado',
                monto: formatata.monto
            }
        };
        if (this.categoria === "6") {
            formatata.medioPagoID = 3;
        }
        this.movimientosProvider.postMovimiento(formatata)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show("El movimiento ha sido registrado", "toast-succeed");
                this.OnCancelForm();
            },
            error => {
                this.errorHandler.handleError(error);
                this.eeloading.close();
            }
            )
    }

    removeControls() {
        Object.keys(this.gastoForm.controls).forEach((element, index) => {
            if (index > 3) this.gastoForm.removeControl(element);
        });
    }

    OnCancelForm() {
        this.removeControls();
        this.props = null;
        this.enableCheque = false;
        if (this.gastoForm.controls['monto'].disabled) this.gastoForm.controls['monto'].enable();
        if (this.myForm) {
            this.myForm.resetForm();
            this.myForm.form.updateValueAndValidity();
            let initialDate = new Date();
            this.gastoForm.controls['fechaMovimiento'].setValue(initialDate);
        }

    }
}



