import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { Global } from './../../../variables/Global';
import { Router } from '@angular/router';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { Component } from '@angular/core';

@Component({
    selector: 'cobros-pendientes',
    template: require('./cobros-pendientes.component.html')
})
export class CobrosPendientesComponent {
    dataSource;
    dataColumn;
    constructor(
        private movimientoProvider: MovimientosProvider,
        private router: Router,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eeToast: EEToast) {
        this.eeloading.open();
        this.movimientoProvider.getCobrosPendientes(false, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];

                data.forEach(element => {
                    let dataElement = {
                        id: element.movimientoID,
                        fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                        descripcion: element.subcategoria.nombre,
                        nombre: element.residente.nombre,
                        monto: '$ ' + element.monto
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataColumn = [{
                    key: 'id',
                    title: 'ID'
                },
                {
                    key: 'fecha',
                    title: 'Fecha'
                },
                {
                    key: 'descripcion',
                    title: 'Descripción'
                },
                {
                    key: 'nombre',
                    title: 'Residente'
                },
                {
                    key: 'monto',
                    title: 'Monto'
                }]
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    selectedCobro(options) {
        console.log(options)
        this.eeloading.open();
        if (options.row.id) {
            this.movimientoProvider.procesarCobro(options.row.id)
                .subscribe(
                (data: any) => {
                    this.eetoast.show('Cobro procesado', 'toast-succeed');
                    this.router.navigate(["app/dashboard"]);
                    this.eeloading.close();
                },
                error => {
                    this.errorHandler.handleError(error);
                    this.eeloading.close();
                });
        }

    }

}



