import createNumberMask from 'text-mask-addons/dist/createNumberMask'


export class EEMasks {
    static tel = ['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];
    static eecurrency = createNumberMask({
        prefix: '$ ',
        suffix: '',
        allowDecimal: true,
        thousandsSeparatorSymbol: '.',
        decimalSymbol: ',',
        decimalLimit: 2,
        integerLimit: 6,
        allowLeadingZeroes: true
    })
    static date = [/[0-31]/, /\d/, '/', /[0-12]/, /\d/, '/', /[0-9]/, /\d/, /\d/, /\d/];
    static chequeMask = [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/];
}


