import { ErrorHandler } from './../../../handlers/errorHandler';
import { APPCONFIG } from './../../config';
import { EEValidators } from './../../../utils/eevalidators';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Global } from './../../../variables/Global';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, ViewChild } from '@angular/core';

@Component({
    selector: 'transferencia-cb',
    template: require('./transferencia-cb.component.html'),
})
export class TransferenciaCBComponent {
    private currencyMask = EEMasks.eecurrency;
    private dateMask = EEMasks.date;
    private chequeMask = EEMasks.chequeMask;
    private enabledChequeInputs = false;
    @ViewChild(FormGroupDirective) myForm;
    transferenciaForm: FormGroup;
    formSubmitted = false;
    constructor(
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private router: Router,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private movimientosProvider: MovimientosProvider) {
    }

    ngOnInit() {
        this.transferenciaForm = this.formBuilder.group({
            tipo: this.formBuilder.control(null, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(null, [Validators.required]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
        });
        let initialDate = new Date();
        this.transferenciaForm.controls['fechaMovimiento'].setValue(initialDate);

        this.transferenciaForm.controls['tipo'].valueChanges.subscribe((value) => {
            if (Number(value) === 73) {
                this.enabledChequeInputs = true;
                this.enableChequeInputs();
            } else {
                this.enabledChequeInputs = false;
                this.disableChequeInputs();
            }
        })
    }

    onSubmit() {
        if (!this.transferenciaForm.valid) return;
        let transferenciaModel = this.transferenciaForm.value;
        transferenciaModel.tipoMovimiento = 3;
        transferenciaModel.residenciaID = APPCONFIG.brand.id;
        let tipoTransferencia;
        if (this.transferenciaForm.controls['tipo'].disabled) {
            tipoTransferencia = this.transferenciaForm.controls['tipo']['_value'];
        }
        if (typeof (transferenciaModel.monto) === 'string') {
            transferenciaModel.monto = transferenciaModel.monto.replace('$ ', '');
            if (transferenciaModel.monto.indexOf('.') > -1) {
                transferenciaModel.monto = transferenciaModel.monto.replace('.', '');
            }
        }
        transferenciaModel.subcategoriaID = transferenciaModel.tipo || tipoTransferencia;
        if (this.enabledChequeInputs) {
            transferenciaModel.cheque = {
                numero: transferenciaModel.nroCheque,
                banco: transferenciaModel.banco,
                fechaEmision: transferenciaModel.fechaMovimiento,
                fechaPago: transferenciaModel.fechaMovimiento,
                tipo: 'TRANSF',
                residenciaID: transferenciaModel.residenciaID,
                destino: 'A CAJA',
                estado: 'registrado',
                monto: transferenciaModel.monto
            }
        }
        this.eeloading.open();
        this.movimientosProvider
            .postMovimiento(transferenciaModel)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show('Transferencia realizada', 'toast-succeed');
                this.router.navigate(["app/dashboard"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    enableChequeInputs() {
        this.transferenciaForm.addControl('nroCheque', new FormControl(null, Validators.required));
        this.transferenciaForm.addControl('banco', new FormControl('Galicia', Validators.required));
    }

    disableChequeInputs() {
        this.transferenciaForm.removeControl('nroCheque');
        this.transferenciaForm.removeControl('banco');
    }

    onResetForm() {
        this.enabledChequeInputs = false;
        this.formSubmitted = false;
        if (this.myForm) {
            this.myForm.resetForm();
            this.myForm.form.updateValueAndValidity();
            let initialDate = new Date();
            this.transferenciaForm.controls['fechaMovimiento'].setValue(initialDate);
        }
    }

}



