import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
var _ = require('lodash');
@Component({
    selector: 'resumen-cuenta-res',
    template: require('./resumen-cuenta-res.component.html'),
})
export class ResumenCuentaResComponent {
    public AppConfig: any;
    private nombreCuenta;
    private saldoCuenta = 0;
    private props;
    private dataSource;
    private dataColumn;
    resumenCuentaForm: FormGroup;
    formSubmitted = false;
    constructor(
        private movimientosProvider: MovimientosProvider,
        private formBuilder: FormBuilder,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eetoast: EEToast) {
        this.eeloading.open();
        this.getMovimientosByResidencia();
    }

    ngOnInit() {
        this.AppConfig = APPCONFIG;
        this.dataColumn = [
            {
                key: 'id',
                title: 'ID'
            },
            {
                key: 'fecha',
                title: 'Fecha'
            },
            {
                key: 'subcategoria',
                title: 'Subcategoria'
            },
            {
                key: 'detalle1',
                title: 'Detalle 1'
            },
            {
                key: 'detalle2',
                title: 'Detalle 2'
            },
            {
                key: 'monto',
                title: 'Monto'
            },
            {
                key: 'actual',
                title: 'Actual'
            }];

    }

    getMovimientosByResidencia() {
        this.eeloading.open();
        this.movimientosProvider
            .GetResumenCuentaByResidencia(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                this.nombreCuenta = data.nombre;
                this.saldoCuenta = data.saldo;
                if (this.saldoCuenta < 0) {
                    this.eetoast.show('Advertencia, Saldo negativo.', 'toast-warning')
                };
                let newSaldo = data.saldoInicial;   //0;
                data.movimientos.forEach(element => {
                    let monti = 0;
                    let coord = false;
                    if (element.tipoMovimiento === 1 && element.personalID != null) {
                        coord = true;
                    }
                    else {
                        if (element.tipoMovimiento === 1 ||
                            (element.tipoMovimiento === 3 && element.subcategoriaID === 64) ||
                            (element.tipoMovimiento === 4 && element.tipoAjuste === 'H')) {
                            monti = element.monto * (-1);
                        }
                        else {
                            if (!(element.tipoMovimiento === 3 && (element.subcategoriaID === 72 || element.subcategoriaID === 73))) {
                                monti = element.monto;
                            }
                        }
                    }
                    newSaldo = newSaldo + monti;
                    let dataElement = {
                        id: element.movimientoID,
                        fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                        subcategoria: element.subcategoria.nombre,
                        detalle1: element.detalle1,
                        detalle2: element.detalle2,
                        // monto: (element.tipoMovimiento === 1 ||
                        //     (element.tipoMovimiento === 3 && element.subcategoriaID === 64) ||
                        //     (element.tipoMovimiento === 4 && element.tipoAjuste === 'H'))
                        //     ? '($ ' + element.monto + ')' : '$ ' + element.monto,
                        monto: monti < 0 ? '($ ' + element.monto + ')' : '$ ' + element.monto,
                        actual: coord != true ? newSaldo : ''
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataSource = _.reverse(this.dataSource);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
}


