import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
var _ = require('lodash');
@Component({
    selector: 'resumen-cuenta',
    template: require('./resumen-cuenta.component.html'),
})
export class ResumenCuentaComponent {
    private nombreCuenta;
    private saldoCuenta = 0;
    private props;
    private dataSourcePendientes;
    private dataSourceMovimientos;
    private dataColumnPendientes;
    private dataColumnMovimientos;
    hidePendientes: boolean = true;
    hasPendingRecords;
    resumenCuentaForm: FormGroup;
    formSubmitted = false;
    constructor(
        private movimientosProvider: MovimientosProvider,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private dataStg: DataStorageProvider) {
        this.eeloading.open();
        this.getMovimientosByPersonal(this.dataStg.getValue("username"));
    }

    ngOnInit() {
        this.dataColumnPendientes = [{
            key: 'id',
            title: 'ID'
        },
        {
            key: 'fecha',
            title: 'Fecha'
        },
        {
            key: 'residente',
            title: 'Residente'
        },
        {
            key: 'monto',
            title: 'Monto'
        }];
        this.dataColumnMovimientos = [{
            key: 'id',
            title: 'ID'
        },
        {
            key: 'fecha',
            title: 'Fecha'
        },
        {
            key: 'subcategoria',
            title: 'Subcategoria'
        },
        {
            key: 'montoIngresos',
            title: 'Monto Ingresos'
        },
        {
            key: 'montoEgresos',
            title: 'Monto Egresos'
        }, {
            key: 'actual',
            title: 'Actual'
        }];

    }

    getMovimientosByPersonal(username) {
        this.eeloading.open();
        this.movimientosProvider
            .GetResumenCuentaByPersonal(username, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceMovimientos = [];
                this.dataSourcePendientes = [];
                this.nombreCuenta = data.nombre;
                this.saldoCuenta = data.saldo;
                if (this.saldoCuenta < 0) {
                    this.eetoast.show('Advertencia, Saldo negativo.', 'toast-warning')
                };
                this.hasPendingRecords = data.pendientes.length > 0;
                //data.movimientos = _.reverse(data.movimientos);
                let montoInitial = data.saldo;
                data.pendientes.forEach(element => {
                    let dataElement = {
                        id: element.movimientoID,
                        fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                        residente: element.residente.nombre, 
                        monto: '$ ' + element.monto
                    }
                    this.dataSourcePendientes.push(dataElement)
                });
                let newSaldo = data.saldoInicial;   //0;
                data.movimientos.forEach(element => {
                    // if (element.tipoMovimiento !== 2) {
                    //     if (element.tipoMovimiento === 1) {
                    //         montoInitial = montoInitial + element.monto
                    //     }
                    //     else {
                    //         montoInitial = montoInitial - element.monto
                    //     }
                    //     let dataElement = {
                    //         id: element.movimientoID,
                    //         fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                    //         subcategoria: element.subcategoria.nombre,
                    //         montoIngresos: (element.tipoMovimiento === 2 && element.estadoCobro == true ||
                    //             element.tipoMovimiento === 3 && element.subcategoriaID == 64
                    //         ) ? '$ ' + element.monto : '',
                    //         montoEgresos: (element.tipoMovimiento === 1 ||
                    //             element.tipoMovimiento === 3 && element.subcategoriaID == 65
                    //         ) ? '($ ' + element.monto + ')' : '',
                    //         actual: montoInitial
                    //     }
                    //     this.dataSourceMovimientos.push(dataElement)
                    // }
                    let monti = 0;
                    if (element.tipoMovimiento === 3 && element.subcategoriaID == 64) {
                        monti = element.monto;
                    }
                    else {
                        if (element.tipoMovimiento === 1 || element.tipoMovimiento === 3 && element.subcategoriaID == 65) {
                            monti = element.monto * (-1);
                        }
                    }
                    newSaldo += monti;
                    let dataElement = {
                        id: element.movimientoID,
                        fecha: element.fechaMovimiento.split('T')[0],
                        subcategoria: element.subcategoria.nombre,
                        montoIngresos: (element.tipoMovimiento === 3 && element.subcategoriaID == 64) ? '$ ' + element.monto : '',
                        montoEgresos: (element.tipoMovimiento === 1 || element.tipoMovimiento === 3 && element.subcategoriaID == 65) ? '($ ' + element.monto + ')' : '',
                        actual: newSaldo
                    }
                    this.dataSourceMovimientos.push(dataElement)
                });
                //this.dataSourceMovimientos = _.reverse(this.dataSourceMovimientos);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    hideMovimientosPendientes() {
        this.hidePendientes = !this.hidePendientes;
    }

}



