import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PeriodoProvider } from './../../../providers/periodo-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    selector: 'periodo-detalle',
    template: require('./periodo-detalle.component.html'),
})
export class PeriodoDetalleComponent {
    private titleForm;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    periodoDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    constructor(
        private periodoProvider: PeriodoProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private location: Location) {
        //this.eeloading.open();
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
            });
        this.titleForm = this.id ? 'Período ' : 'Agregar Período';
        this.buildForm();
        if (!this.id) {
            this.onAddForm();
        }
        else {
            this.getPeriodoById();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm;
                this.onDeleteForm();
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }
        }
    }

    getPeriodoById() {
        this.titleForm == "";
        this.eeloading.open();
        this.periodoProvider
            .GetPeriodoById(this.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                //this.titleForm += data.periodo;
                this.periodoDetalleForm.controls['periodo'].setValue(data.periodo);
                this.periodoDetalleForm.controls['visitaHombre'].setValue(data.visitaHombre);
                this.periodoDetalleForm.controls['visitaMujer'].setValue(data.visitaMujer);
                this.periodoDetalleForm.controls['visitaPareja'].setValue(data.visitaPareja);
                this.periodoDetalleForm.controls['hhNecesarias'].setValue(data.hhNecesarias);
                this.periodoDetalleForm.controls['hhUtilizadas'].setValue(data.hhUtilizadas);
                this.periodoDetalleForm.controls['ausentismoOperativo'].setValue(data.ausentismoOperativo);
                this.periodoDetalleForm.controls['ausentismoReal'].setValue(data.ausentismoReal);
                this.periodoDetalleForm.controls['horasPagas'].setValue(data.horasPagas);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.periodoDetalleForm = this.formBuilder.group({
            periodo: this.formBuilder.control(null, [Validators.required]),
            visitaHombre: this.formBuilder.control(null, [Validators.required]),
            visitaMujer: this.formBuilder.control(null, [Validators.required]),
            visitaPareja: this.formBuilder.control(null, [Validators.required]),
            hhNecesarias: this.formBuilder.control(null, [Validators.required]),
            hhUtilizadas: this.formBuilder.control(null, [Validators.required]),
            ausentismoOperativo: this.formBuilder.control(null, [Validators.required]),
            ausentismoReal: this.formBuilder.control(null, [Validators.required]),
            horasPagas: this.formBuilder.control(null, [Validators.required])
        });
    }

    enableForm() {
        if (this.modeAddForm) { this.periodoDetalleForm.controls['periodo'].enable(); }
        else { this.periodoDetalleForm.controls['periodo'].disable(); }
        this.periodoDetalleForm.controls['visitaHombre'].enable();
        this.periodoDetalleForm.controls['visitaMujer'].enable();
        this.periodoDetalleForm.controls['visitaPareja'].enable();
        this.periodoDetalleForm.controls['hhNecesarias'].enable();
        this.periodoDetalleForm.controls['hhUtilizadas'].enable();
        this.periodoDetalleForm.controls['ausentismoOperativo'].enable();
        this.periodoDetalleForm.controls['ausentismoReal'].enable();
        this.periodoDetalleForm.controls['horasPagas'].enable();
    }

    disableForm() {
        this.periodoDetalleForm.controls['periodo'].disable();
        this.periodoDetalleForm.controls['visitaHombre'].disable();
        this.periodoDetalleForm.controls['visitaMujer'].disable();
        this.periodoDetalleForm.controls['visitaPareja'].disable();
        this.periodoDetalleForm.controls['hhNecesarias'].disable();
        this.periodoDetalleForm.controls['hhUtilizadas'].disable();
        this.periodoDetalleForm.controls['ausentismoOperativo'].disable();
        this.periodoDetalleForm.controls['ausentismoReal'].disable();
        this.periodoDetalleForm.controls['horasPagas'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    // onResetForm() {
    //     this.periodoDetalleForm.reset();
    //     this.formSubmitted = false;
    // }

    onSubmit() {
        if (!this.periodoDetalleForm.valid) return;
        let periodoModel = this.periodoDetalleForm.value;
        this.eeloading.open();
        if (this.modeAddForm) {
            periodoModel.residenciaID = APPCONFIG.brand.id;
            this.periodoProvider
                .PostPeriodo(periodoModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Periodo agregado', 'toast-succeed');
                    //this.router.navigate(["app/dashboard"]);
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
        else if (this.modeEditForm) {
            periodoModel.periodoID = this.id;
            this.periodoProvider
                .PutPeriodo(periodoModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Periodo actualizado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )

        }
    }

    onDelete() {
        //let periodoModel = this.periodoDetalleForm.value;
        //periodoModel.residenciaID = APPCONFIG.brand.id;
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.periodoProvider
                .DeletePeriodo(this.id)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Periodo borrado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



