import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { BehaviorSubject } from "rxjs/Rx";

@Component({
    selector: 'resumen-cuenta-all',
    template: require('./resumen-cuenta-all.component.html'),
})
export class ResumenCuentaAllComponent {
    private isPersonalChecked: boolean;
    private saldoPersonal = 0;
    private props;
    private dataSourceMovimientos;
    private dataColumnMovimientos;
    private hasRecords;
    private sourceChangesEmitter = new BehaviorSubject(null);
    resumenCuentaAllForm: FormGroup;
    constructor(
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private movimientosProvider: MovimientosProvider) {
        this.getPersonalesByRole();
    }

    ngOnInit() {
        this.resumenCuentaAllForm = this.formBuilder.group({
            personalID: this.formBuilder.control(null, [Validators.required])
        });
        this.resumenCuentaAllForm.controls['personalID']
            .valueChanges
            .subscribe((value) => {
                this.isPersonalChecked = true;
                this.getMovimientosByPersonal(value);
            });

        this.getDataColumns();
    }

    getPersonalesByRole() {
        this.props = {};
        this.eeloading.open();
        this.movimientosProvider
            .getPersonalesByRole("COORD", APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.props.empleados = data;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }

    getMovimientosByPersonal(userid) {
        this.eeloading.open();
        this.movimientosProvider
            .GetResumenCuentaByPersonalId(userid, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceMovimientos = [];
                this.hasRecords = data.movimientos.length > 0;
                this.saldoPersonal = data.saldo;
                data.movimientos.forEach(element => {
                    let dataElement = {
                        id: element.movimientoID,
                        fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                        subcategoria: element.subcategoria.nombre,
                        montoIngresos: ((element.tipoMovimiento === 3 && element.subcategoriaID === 64) || element.tipoMovimiento === 2) ? '$ ' + element.monto : '',
                        montoEgresos: ((element.tipoMovimiento === 3 && element.subcategoriaID === 65) || element.tipoMovimiento === 1) ? '($ ' + element.monto + ')' : ''
                    }

                    this.dataSourceMovimientos.push(dataElement);
                    this.sourceChangesEmitter.next(this.dataSourceMovimientos);
                });

            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    getDataColumns() {
        this.dataColumnMovimientos = [{
            key: 'id',
            title: 'ID'
        },
        {
            key: 'fecha',
            title: 'Fecha'
        },
        {
            key: 'subcategoria',
            title: 'Subcategoria'
        },
        {
            key: 'montoIngresos',
            title: 'Monto Ingresos'
        },
        {
            key: 'montoEgresos',
            title: 'Monto Egresos'
        }];
    }

}



