import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { UsuarioProvider } from './../../../providers/usuario-provider';
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'usuarios',
    template: require('./usuarios.component.html'),
})
export class UsuariosComponent {
    private hasRecords;
    private dataSource;
    private dataSet;
    private dataColumn;
    constructor(
        private router: Router,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eeToast: EEToast,
        private usuarioProvider: UsuarioProvider,
        private dataStg: DataStorageProvider) {
            this.getUsuarios();
        }

    ngOnInit() {
        this.dataColumn = [                             
            // {
            //     key: 'id',
            //     title: 'ID'
            // },            
            {
                key: 'username',
                title: 'Usuario'
            },
            {
                key: 'nombre',
                title: 'Nombre'
            },
            {
                key: 'rol',
                title: 'Rol'
            }];
    }

    getUsuarios() {
        this.eeloading.open();
        this.usuarioProvider
            .GetUsuarios()
            .subscribe(
            data => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.length > 0;
                data.forEach(element => {
                    let dataElement = {
                        username: element.UserName,
                        nombre: element.nombre,
                        rol: element.rol
                    }
                    this.dataSource.push(dataElement);
                });
                
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    onAddUsuario() {
        this.router.navigate(['/app/usuario-detalle']);
    }

    onEditUsuario(obj) {
        this.router.navigate(['/app/usuario-detalle'], {queryParams: {id: obj.id}});
    }

    onDeleteUsuario(obj) {
        this.router.navigate(['/app/usuario-detalle'], {queryParams: {id: obj.id, action: 'd'}});
    }

}



