import { Router } from '@angular/router';
import { ChequesProvider } from './../../../providers/cheques-provider';
import { APPCONFIG } from './../../config';
import { EEToast } from './../../../providers/ee-toast-provider';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { EELoading } from './../../../providers/ee-loading-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component } from '@angular/core';

@Component({
    selector: 'cheque-add',
    template: require('./cheque-add.component.html'),
})
export class ChequeAddComponent {
    private currencyMask = EEMasks.eecurrency;
    private dateMask = EEMasks.date;
    private chequeMask = EEMasks.chequeMask;
    chequeForm: FormGroup;
    formSubmitted = false;

    constructor(
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private router: Router,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private chequeProvider: ChequesProvider
    ) {
        this.chequeForm = this.formBuilder.group({
            nroCheque: this.formBuilder.control(null, [Validators.required]),
            emitidoCheque: this.formBuilder.control(null, [Validators.required])
        });
        let initialDate = new Date();
        this.chequeForm.controls['emitidoCheque'].setValue(initialDate);
    }

    onSubmit() {
        if (!this.chequeForm.valid) return;
        let _chequeModel = this.chequeForm.value;
        let chequeModel = {
            numero: _chequeModel.nroCheque,
            banco: '-',
            fechaEmision: _chequeModel.emitidoCheque,
            fechaVencimiento: _chequeModel.emitidoCheque,
            tipo: 'CANCEL',
            estado: (APPCONFIG.rol == 'ADMIN') ? true : false,
            residenciaID: APPCONFIG.brand.id,
            monto: 0
        };
        this.chequeProvider
            .addCheque(chequeModel)
            .subscribe(
            data => {
                this.eetoast.show('Cheque registrado', 'toast-succeed');
                this.router.navigate(["app/cheques/emitidos"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }
}



