import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { APPCONFIG } from './../../config';
import { EEValidators } from './../../../utils/eevalidators';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Global } from './../../../variables/Global';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component } from '@angular/core';

@Component({
    selector: 'ajuste',
    template: require('./ajuste.component.html')
})
export class AjusteComponent {
    private currencyMask = EEMasks.eecurrency;
    private dateMask = EEMasks.date;
    private checkIdx;
    ajusteForm: FormGroup;
    formSubmitted = false;
    constructor(
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private router: Router,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private dataStg: DataStorageProvider,
        private movimientosProvider: MovimientosProvider) {
    }

    ngOnInit() {
        this.ajusteForm = this.formBuilder.group({
            tipo: this.formBuilder.control(null, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(null, [Validators.required]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
            tipoAjuste: this.formBuilder.control(null, [Validators.required]),
        });
        let initialDate = new Date();
        this.ajusteForm.controls['fechaMovimiento'].setValue(initialDate);
        this.ajusteForm.controls['tipoAjuste'].setValue("H");
    }

    onSubmit() {
        if (!this.ajusteForm.valid) return;
        let ajusteModel = this.ajusteForm.value;
        ajusteModel.tipoMovimiento = 4;
        ajusteModel.residenciaID = this.dataStg.getValue("residencia") || APPCONFIG.brand.id;
        if (typeof (ajusteModel.monto) === 'string') {
            ajusteModel.monto = ajusteModel.monto.replace('$ ', '');
            if (ajusteModel.monto.indexOf('.') > -1) {
                ajusteModel.monto = ajusteModel.monto.replace('.', '');
            }
        }
        ajusteModel.subcategoriaID = ajusteModel.tipo;
        ajusteModel.tipoAjuste = ajusteModel.tipoAjuste;
        this.eeloading.open();
        this.movimientosProvider
            .postMovimiento(ajusteModel)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show('Ajuste realizado', 'toast-succeed');
                this.router.navigate(["app/dashboard"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    OnResetForm() {
        this.ajusteForm.reset();
        this.formSubmitted = false;
    }

}



