import { EEGerCommons } from './../../../utils/ee-ger.common';
import { MatDialog } from '@angular/material';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { ChequesProvider } from './../../../providers/cheques-provider';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { Component } from '@angular/core';
import { Router } from "@angular/router";
var _array = require('lodash/collection');
@Component({
    selector: 'cheques-emitidos',
    template: require('./cheques-emitidos.component.html'),
})
export class ChequesEmitidosComponent {
    dataSourceEmitidos;
    hasRecords;
    today;
    dataColumnEmitidos;

    constructor(
        private movimientoProvider: MovimientosProvider,
        private chequesProvider: ChequesProvider,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private router: Router,
        private eeToast: EEToast,
        private dialog: MatDialog,
        private dataStg: DataStorageProvider) {
        this.eeloading.open();
        this.today = new Date();
        let _residenciaID = APPCONFIG.brand.id || this.dataStg.getValue('residencia');
        this.chequesProvider
            .getChquesInternos(_residenciaID)
            .subscribe(
            (_cheques: any) => {
                this.movimientoProvider.getMovimientosByMedio(3, _residenciaID, 'EMI')
                    .subscribe(
                    (data: any) => {
                        this.eeloading.close();
                        this.dataSourceEmitidos = [];

                        this.hasRecords = data.length > 0;
                        if (this.hasRecords) {
                            data.forEach(element => {
                                let dataElement = {
                                    fecha: EEGerCommons.formattedDate(element.fechaMovimiento),
                                    chequeID: element.cheque.chequeID,
                                    fechaPago: element.cheque.fechaPago ? EEGerCommons.formattedDate(element.cheque.fechaPago) : '',
                                    tipo: element.cheque.tipo,
                                    monto: '$ ' + element.monto,
                                    numero: element.cheque.numero,
                                    banco: element.cheque.banco,
                                    destino: element.cheque.destino,
                                    cubierto: Boolean(element.cheque.estado === 'procesado')
                                };
                                this.dataSourceEmitidos.push(dataElement)
                            });
                        }
                        if (_cheques.chequesCancelados.length > 0) {
                            _cheques.chequesCancelados.forEach(element => {
                                let dataElement = {
                                    fecha: EEGerCommons.formattedDate(element.fechaVencimiento),
                                    chequeID: element.chequeID,
                                    fechaPago: element.fechaPago ? EEGerCommons.formattedDate(element.fechaPago) : '',
                                    tipo: element.tipo,
                                    monto: '-',
                                    numero: element.numero,
                                    banco: element.banco,
                                    destino: '-',
                                    cubierto: true
                                };
                                this.dataSourceEmitidos.push(dataElement)
                            });
                        }
                        if (_cheques.chequesTransferidos.length > 0) {
                            _cheques.chequesTransferidos.forEach(element => {
                                let dataElement = {
                                    fecha: EEGerCommons.formattedDate(element.fechaEmision),
                                    chequeID: element.chequeID,
                                    tipo: element.tipo,
                                    monto: '$ ' + element.monto,
                                    numero: element.numero,
                                    banco: element.banco,
                                    destino: element.destino,
                                    cubierto: true
                                };
                                this.dataSourceEmitidos.push(dataElement)
                            });
                        }

                        this.dataSourceEmitidos = _array.orderBy(this.dataSourceEmitidos, ['numero'], ['asc']);
                        this.dataColumnEmitidos = [{
                            key: 'fecha',
                            title: 'Fecha'
                        },
                        {
                            key: 'fechaPago',
                            title: 'Fecha Pago'
                        },
                        {
                            key: 'monto',
                            title: 'Monto'
                        },
                        {
                            key: 'numero',
                            title: 'Numero'
                        },
                        {
                            key: 'banco',
                            title: 'Banco'
                        },
                        {
                            key: 'destino',
                            title: 'Destino'
                        }];
                    },
                    error => {
                        this.eeloading.close();
                        this.errorHandler.handleError(error);
                    });
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    confirmarPago(cheque) {
        console.log("el cheque", cheque);
        this.eeloading.open();
        if (cheque) {
            this.chequesProvider.confirmarCheque(cheque.chequeID, 'pago')
                .subscribe(
                (data: any) => {
                    this.eeToast.show('Cheque confirmado', 'toast-succeed');
                    this.router.navigate(["app/dashboard"]);
                    this.eeloading.close();
                },
                error => {
                    this.errorHandler.handleError(error);
                    this.eeloading.close();
                });
        }
    }

    onCancelCheque() {
        this.router.navigate(["app/cheques/add"]);
    }
}



