import { ErrorHandler } from './../../../handlers/errorHandler';
import { APPCONFIG } from './../../config';
import { EEValidators } from './../../../utils/eevalidators';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Global } from './../../../variables/Global';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component } from '@angular/core';

@Component({
    selector: 'ingreso',
    template: require('./ingreso.component.html'),
})
export class IngresoComponent {
    private currencyMask = EEMasks.eecurrency;
    private dateMask = EEMasks.date;
    private chequeMask = EEMasks.chequeMask;
    private checkIdx;
    private enabledChequeInputs: boolean = false;
    ingresoForm: FormGroup;
    formSubmitted = false;
    constructor(
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private router: Router,
        private eetoast: EEToast,
        private movimientosProvider: MovimientosProvider) {
    }

    ngOnInit() {
        this.ingresoForm = this.formBuilder.group({
            tipo: this.formBuilder.control(null, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(null, [Validators.required]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
        });
        let initialDate = new Date();
        this.ingresoForm.controls['fechaMovimiento'].setValue(initialDate);
        this.ingresoForm.controls['tipo'].valueChanges.subscribe((value) => {
            if (value === '76') {
                this.enableChequeInputs();
            } else {
                this.disableChequeInputs();
            }
        })
        //this.ingresoForm.controls['tipo'].setValue(70);
    }

    enableChequeInputs() {
        this.enabledChequeInputs = true;
        this.ingresoForm.addControl('nroCheque', new FormControl(null, Validators.required));
        this.ingresoForm.addControl('banco', new FormControl('Galicia', Validators.required));
        this.ingresoForm.addControl('emitidoCheque', new FormControl('', Validators.required));
    }

    disableChequeInputs() {
        this.enabledChequeInputs = false;
        this.ingresoForm.removeControl('nroCheque');
        this.ingresoForm.removeControl('banco');
        this.ingresoForm.removeControl('emitidoCheque');
    }

    onSubmit() {
        if (!this.ingresoForm.valid) return;
        let ingresoModel = this.ingresoForm.value;
        ingresoModel.tipoMovimiento = 5;
        ingresoModel.residenciaID = APPCONFIG.brand.id;
        if (typeof (ingresoModel.monto) === 'string') {
            ingresoModel.monto = ingresoModel.monto.replace('$ ', '');
            if (ingresoModel.monto.indexOf('.') > -1) {
                ingresoModel.monto = ingresoModel.monto.replace('.', '');
            }
        }
        ingresoModel.subcategoriaID = ingresoModel.tipo;

        if (this.enabledChequeInputs) {
            ingresoModel.medioPagoID = 3;
            ingresoModel.cheque = {
                numero: ingresoModel.nroCheque,
                banco: ingresoModel.banco,
                fechaEmision: ingresoModel.fechaMovimiento,
                fechaVencimiento: ingresoModel.emitidoCheque,
                tipo: 'REC',
                estado: (APPCONFIG.rol == 'ADMIN') ? true : false,
                residenciaID: APPCONFIG.brand.id,
                monto: ingresoModel.monto
            }
        }
        this.eeloading.open();
        this.movimientosProvider
            .postMovimiento(ingresoModel)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show('Ingreso registrado', 'toast-succeed');
                this.router.navigate(["app/dashboard"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    OnResetForm() {
        this.ingresoForm.reset();
        this.formSubmitted = false;
    }
}



