import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { APPCONFIG } from './../../config';
import { EEValidators } from './../../../utils/eevalidators';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { Global } from './../../../variables/Global';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
var _ = require('lodash');
@Component({
    selector: 'cobro',
    template: require('./cobro.component.html'),
})
export class CobroComponent {
    private currencyMask = EEMasks.eecurrency;
    private dateMask = EEMasks.date;
    private chequeMask = EEMasks.chequeMask;
    private enableCheque;
    private isResidenteChecked: boolean;
    private cuotaResidente: number = 0;
    private extrasResidente: number = 0;
    private props;
    private startViewDate;
    @ViewChild(FormGroupDirective) myForm;
    cobroForm: FormGroup;
    formSubmitted = false;
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eetoast: EEToast,
        private dataStg: DataStorageProvider,
        private movimientosProvider: MovimientosProvider) {
        this.getCobrosData()
    }

    ngOnInit() {
    }

    buildForm() {
        this.startViewDate = EEGerCommons.limit5Days();
        //this.startViewDate = "21/01/2018";
        console.log(this.startViewDate);
        this.cobroForm = this.formBuilder.group({
            residenteID: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(null, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required]),
            medioPagoID: this.formBuilder.control(null, [Validators.required]),
            //periodo: this.formBuilder.control(null, [Validators.nullValidator]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
        });
        let initialDate = new Date();
        this.cobroForm.controls['fechaMovimiento'].setValue(initialDate);
        this.cobroForm.controls['residenteID']
            .valueChanges
            .subscribe((value) => {
                if (value) {
                    var dataResidente = this.getDataResidente(value);
                    this.cuotaResidente = dataResidente.cuota;
                    this.extrasResidente = dataResidente.extras;
                    this.cobroForm.controls['monto'].setValue('$ ' + (parseFloat(dataResidente.cuota) + parseFloat(dataResidente.extras)));
                    this.isResidenteChecked = true;
                }
            });
        this.cobroForm.controls['medioPagoID']
            .valueChanges
            .subscribe((value) => {
                if (value === 3) {
                    this.enableChequeInputs();
                } else {
                    this.removeChequeInputs();
                    this.enableCheque = false;
                }
            })
    }

    enableChequeInputs() {
        this.enableCheque = true;
        this.cobroForm.addControl('nroCheque', new FormControl(null, Validators.required));
        this.cobroForm.addControl('banco', new FormControl(null, Validators.required));
        this.cobroForm.addControl('emitidoCheque', new FormControl(new Date(), Validators.required));
        this.cobroForm.updateValueAndValidity();
    }
    removeChequeInputs() {
        this.cobroForm.removeControl('nroCheque');
        this.cobroForm.removeControl('banco');
        this.cobroForm.removeControl('emitidoCheque');
        this.cobroForm.updateValueAndValidity();
    }
    getCobrosData() {
        this.props = {};

        this.buildForm();
        let residenciaID = this.dataStg.getValue('residencia');
        if (residenciaID) {
            this.eeloading.open();
            this.movimientosProvider
                .getCobrosData(residenciaID)
                .subscribe(
                (data: any) => {
                    if (data && data.residentes && data.residentes.length > 0) {
                        data.residentes.forEach(element => {
                            element['fname'] = element.nombre.split(' ')[0];
                            element['lname'] = element.nombre.split(' ')[1];
                        });
                        data.residentes = _.orderBy(data.residentes, ['lname'], ['asc']);
                    }
                    this.props = data;
                    this.eeloading.close();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                })
        } else {
            this.eetoast.show('Session expirada', 'toast-error');
        }

    }

    getDataResidente(residenteID) {
        let data;
        this.props.residentes.forEach(element => {
            if (residenteID === element.residenteID) {
                data = element;
            }
        });
        return data;
    }

    onSubmit() {
        if (!this.cobroForm.valid) return;
        let cobroModel = this.cobroForm.value;
        cobroModel.tipoMovimiento = 2;
        cobroModel.residenciaID = APPCONFIG.brand.id;
        cobroModel.monto = cobroModel.monto.replace('$ ', '');
        if (cobroModel.monto.indexOf('.') > -1) {
            cobroModel.monto = cobroModel.monto.replace('.', '');
        }
        if (cobroModel.medioPagoID && cobroModel.medioPagoID === 3) {
            cobroModel.cheque = {
                numero: this.cobroForm.value.nroCheque,
                banco: this.cobroForm.value.banco,
                fechaEmision: this.cobroForm.value.emitidoCheque,
                fechaVencimiento: this.cobroForm.value.emitidoCheque,
                tipo: 'REC',
                estado: (APPCONFIG.rol == 'ADMIN') ? true : false,
                residenciaID: cobroModel.residenciaID,
                monto: cobroModel.monto
            }
        };
        cobroModel.subcategoriaID = 63;
        this.eeloading.open();
        this.movimientosProvider
            .postMovimiento(cobroModel)
            .subscribe(
            (data: any) => {
                this.eetoast.show('Cobro registrado', 'toast-succeed');
                this.OnCancelForm();
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }

    OnCancelForm() {
        if (this.cobroForm.controls['fechaMovimiento']['_value'] === 3) this.removeChequeInputs();
        if (this.myForm) {
            this.myForm.resetForm();
            this.myForm.form.updateValueAndValidity();
            let initialDate = new Date();
            this.cobroForm.controls['fechaMovimiento'].setValue(initialDate);
            this.isResidenteChecked = !this.isResidenteChecked;
        }

    }
}



