import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
var _ = require('lodash');
import * as moment from 'moment';
@Component({
    selector: 'bancodiario',
    template: require('./bancodiario.component.html')
})
export class BancodiarioComponent {
    public AppConfig: any;
    private nombreCuenta;
    private saldoCuenta = 0;
    private props;
    private dataSourcePendientes;
    private dataSourceMovimientos;
    private dataColumnPendientes;
    private dataColumnMovimientos;
    resumenCuentaForm: FormGroup;
    formSubmitted = false;
    constructor(
        private movimientosProvider: MovimientosProvider,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast) {
        this.eeloading.open();
        this.getBancoDiario();
    }

    ngOnInit() {
        this.AppConfig = APPCONFIG;
        this.dataColumnMovimientos = [
            {
                key: 'fecha',
                title: 'Fecha'
            },
            {
                key: 'deposito',
                title: 'Deposito'
            },
            {
                key: 'egresos',
                title: 'Egresos'
            },
            {
                key: 'egresoproy',
                title: 'Egresos Proy'
            },
            {
                key: 'tarjeta',
                title: 'Tarjeta'
            },
            {
                key: 'disponible',
                title: 'Disponible'
            }];

    }

    getBancoDiario() {
        this.eeloading.open();
        this.movimientosProvider
            .GetBancoDiario(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceMovimientos = [];
                this.dataSourcePendientes = [];
                let fechaActual;

                let disponibleActual = 0;
                if (data.length > 0) {
                    data.forEach(element => {
                        //TODO: add tarjeta credito
                        //fechaActual null is the first. fechaActual igual no hago nada
                        if (!fechaActual) fechaActual = element.fecha;
                        let difDays = 0;
                        if (moment(fechaActual).format("YYYY-MM-DD") !== moment(element.fecha).format("YYYY-MM-DD")) {
                            difDays = this.diffDays(fechaActual, element.fecha);
                        }
                        if (difDays > 1) {
                            let to = difDays - 1;
                            let actualMoment = fechaActual;
                            for (let i = 0; i < to; ++i) {
                                actualMoment = this.nextDay(actualMoment);
                                let element = {
                                    fecha: actualMoment.format("DD/MM/YYYY"),
                                    deposito: 0,
                                    egresos: 0,
                                    egresoproy: 0,
                                    tarjeta: '',
                                    disponible: disponibleActual,
                                }
                                this.dataSourceMovimientos.push(element);
                            }
                        } else if (difDays === 1) {
                            fechaActual = element.fecha;
                        }
                        disponibleActual = disponibleActual + element.activoBanco - element.pasivoBanco + element.aPagarCheques - element.aPagarTarjeta;
                        let dataElement = {
                            fecha: moment(element.fecha).format("DD/MM/YYYY"),
                            deposito: element.activoBanco,
                            egresos: element.pasivoBanco,
                            egresoproy: -element.aPagarCheques,
                            tarjeta: '',
                            disponible: disponibleActual,
                        }
                        this.dataSourceMovimientos.push(dataElement);
                    });
                }
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }


    diffDays(fechaActual, fechaSig) {
        var a = moment(fechaActual);
        var b = moment(fechaSig);
        return b.diff(a, 'days');
    }

    nextDay(fechaActual) {
        return moment(fechaActual).add(1, 'day');

    }
}


