import { ErrorHandler } from './../../../handlers/errorHandler';
//import { ChildTable } from './../../../components/common/ee-table/ee-table.component';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PeriodoProvider } from './../../../providers/periodo-provider';
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'periodos',
    template: require('./periodos.component.html'),
})
export class PeriodosComponent {
    private hasRecords;
    private dataSource;
    private dataSet;
    private dataColumn;
    constructor(
        private router: Router,
        private periodoProvider: PeriodoProvider,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eeToast: EEToast) {
        this.eeloading.open();
        this.periodoProvider.GetPeriodos(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.length > 0;
                data.forEach(element => {
                    let visitas = element.visitaHombre + element.visitaMujer + element.visitaPareja
                    let dataElement = {
                        periodoID: element.periodoID,
                        periodo: element.periodo,
                        visitas: visitas,
                        hhUtilizadas: element.hhUtilizadas,
                        ausentismoReal: element.ausentismoReal,
                        horasPagas: element.horasPagas
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataColumn = [{
                    key: 'periodo',
                    title: 'Periodo'
                },
                {
                    key: 'visitas',
                    title: 'Visitas'
                },
                {
                    key: 'hhUtilizadas',
                    title: 'HH.Utilizadas'
                },
                {
                    key: 'ausentismoReal',
                    title: 'Ausent.Real'
                },
                {
                    key: 'horasPagas',
                    title: 'H.Pagas'
                }];
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    onAddForm(obj) {
        this.router.navigate(['/app/periodo-detalle']);
    }

    onSelectedRowEdit(obj) {
        this.router.navigate(['/app/periodo-detalle'], { queryParams: { id: obj.periodoID } });
    }

    onSelectedRowDelete(obj) {
        this.router.navigate(['/app/periodo-detalle'], { queryParams: { id: obj.periodoID, action: 'd' } });
    }

}



