import { ResidenteProvider } from './../../../providers/residente-provider';
import { APPCONFIG } from './../../config';
import { PeriodoProvider } from './../../../providers/periodo-provider';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { EEMasks } from "../../../utils/eemasks";
import { Router } from "@angular/router";

@Component({
    selector: 'cuotas',
    template: require('./cuotas.component.html')
})
export class CuotasComponent {
    private currencyMask = EEMasks.eecurrency;
    private isPeriodoChecked: boolean;
    private props;
    private periodoName;
    private cuotas;
    private cuotasPeriodo;
    private isCuotasFilled;
    private residentes;
    cuotaForm: FormGroup;
    constructor(
        private router: Router,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private residenteProvider: ResidenteProvider,
        private periodoProvider: PeriodoProvider) {
        this.getPeriodosDist();
    }

    getPeriodosDist() {
        this.props = {};
        this.eeloading.open();
        this.periodoProvider
            .GetPeriodosDist(APPCONFIG.brand.id) //APPCONFIG.brand.id
            .subscribe(
            (data: any) => {
                this.props.periodos = data;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    ngOnInit() {
        this.cuotaForm = this.formBuilder.group({
            periodoID: this.formBuilder.control(null, [Validators.required])
        });
        this.cuotaForm.controls['periodoID'].valueChanges.subscribe((value) => {
            this.getCuotasByPeriodo(value)
        });
    }

    getCuotasByPeriodo(value) {
        this.periodoName = this.getPeriodoValue(value);
        this.isPeriodoChecked = true;
        this.eeloading.open();
        this.periodoProvider
            .GetCuotasByPeriodo(this.periodoName, APPCONFIG.brand.id) //APPCONFIG.brand.id
            .subscribe(
            (data: any) => {
                if (data && data.length === 0) {
                    this.getResidentesCuotas();
                } else {
                    this.cuotas = [];
                    let cuotitas = [];
                    data.forEach(element => {
                        let cuotaElement = {
                            nombre: element.residente.nombre,
                            monto: element.monto
                        };
                        cuotitas.push(cuotaElement);
                    });
                    this.cuotas = cuotitas;
                    this.eeloading.close();
                }
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    getResidentesCuotas() {
        this.residenteProvider
            .GetResidentesByStatus(APPCONFIG.brand.id, 'activo')
            .subscribe(
            (data: any) => {
                //render table con la ultima cuota y para llenar los periodos
                this.fillResidentesTable(data);
                this.residentes = data;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    fillResidentesTable(_residentes) {
        _residentes.forEach(element => {
            let montoCuota = "-";
            let cuotaLength = element.cuotas ? element.cuotas.length : 0;
            if (cuotaLength > 0) {
                montoCuota = element.cuotas[cuotaLength - 1].monto.toString();
            };
            element.montoCuota = montoCuota;
            element.monto = "";
        });
        this.cuotas = [];
        this.residentes = _residentes;
    }

    onSubmit() {
        let isValid = true;
        this.residentes.forEach(element => {
            if (element.monto.length === 0) {
                isValid = false;
            }
        });
        if (isValid) {
            this.procesarCuotas();
        } else {
            this.eetoast.show("Hay una o mas cuotas sin ingresar.", "toast-error");
        }
    }

    procesarCuotas() {
        let _cuotasPeriodo = [];
        let periodoName = this.periodoName;
        this.residentes.forEach(element => {
            let _monto = element.monto.replace('$ ', '');
            if (_monto.indexOf('.') > -1) {
                _monto = _monto.replace('.', '');
            }
            let newCuota = {
                residenciaID: APPCONFIG.brand.id,
                residenteID: element.residenteID,
                periodo: periodoName,
                monto: _monto
            }
            _cuotasPeriodo.push(newCuota);
        });
        let periodoModel = {
            periodoID: this.cuotaForm.controls['periodoID']['_value'],
            cuotas: _cuotasPeriodo
        }
        this.eeloading.open();
        this.periodoProvider
            .PutPeriodo(periodoModel)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show('Cuotas cargadas', 'toast-succeed');
                this.router.navigate(["app/dashboard"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }
    getPeriodoValue(id) {
        let _periodoName;
        this.props.periodos.forEach(element => {
            if (element.periodoID === id) {
                _periodoName = element.nombre;
            }
        });
        return _periodoName;
    }
}


