import { ErrorHandler } from './../../../handlers/errorHandler';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { ResidenciaProvider } from './../../../providers/residencia-provider';
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'residencias',
    template: require('./residencias.component.html'),
})
export class ResidenciasComponent {
    private hasRecords;
    dataSource;
    dataSet;
    dataColumn;
    constructor(
        private router: Router,
        private residenciaProvider: ResidenciaProvider,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eeToast: EEToast) {
        this.eeloading.open();
        this.residenciaProvider.GetResidencias()
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.length > 0;
                data.forEach(element => {
                    let total = element.saldoInicialCaja + element.saldoInicialBanco + element.saldoInicialCheques;
                    let dataElement = {
                        residenciaID: element.residenteID,
                        nombre: element.nombre,
                        periodoInicialSaldo: element.periodoInicialSaldo,
                        saldoInicialCaja: element.saldoInicialCaja,
                        saldoInicialBanco: element.saldoInicialBanco,
                        saldoInicialCheques: element.saldoInicialCheques,
                        saldoInicialTotal: total
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataColumn = [
                    // {
                    //     key: 'residenciaID',
                    //     title: 'ID'
                    // },                              
                    {
                        key: 'nombre',
                        title: 'Nombre'
                    },
                    {
                        key: 'periodoInicialSaldo',
                        title: 'Periodo Ini Saldo'
                    },
                    {
                        key: 'saldoInicialCaja',
                        title: 'Saldo Ini Caja'
                    },
                    {
                        key: 'saldoInicialBanco',
                        title: 'Saldo Ini Banco'
                    },
                    {
                        key: 'saldoInicialCheques',
                        title: 'Saldo Ini Cheques'
                    },
                    {
                        key: 'saldoInicialTotal',
                        title: 'Saldo Ini TOTAL'
                    }];
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    // selectedRow(obj) {
    //     this.router.navigate(['/app/residente-detalle'], {queryParams: {id: obj.residenteID}});
    // }

}



