import { EEMasks } from './../../../utils/eemasks';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { Router } from '@angular/router';
import { EELoading } from './../../../providers/ee-loading-provider';
import { MatDialog } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { EeChequesPDialogComponent } from "../../../components/common/ee-cheques-p-dialog/ee-cheques-p-dialog.component";
import { APPCONFIG } from "../../config";
import { EEToast } from "../../../providers/ee-toast-provider";
import { MovimientosProvider } from "../../../providers/movimientos-provider";

@Component({
    selector: 'transferencia-cheque',
    template: require('./transferencia-cheque.component.html'),
})
export class TransferenciaChequeComponent {
    transferenciaChequeForm: FormGroup;
    selectedCheque;
    chequeMask = EEMasks.chequeMask;
    constructor(
        public formBuilder: FormBuilder,
        private dialog: MatDialog,
        private eeloading: EELoading,
        private router: Router,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private movimientosProvider: MovimientosProvider) {

    }

    ngOnInit() {
        this.transferenciaChequeForm = this.formBuilder.group({
            nroCheque: this.formBuilder.control(null, [Validators.required]),
            fechaMovimiento: this.formBuilder.control(null, [Validators.required]),
            tipo: this.formBuilder.control(null, [Validators.required]),
            descripcion: this.formBuilder.control(null, [Validators.nullValidator]),
        });
        let initialDate = new Date();
        this.transferenciaChequeForm.controls['fechaMovimiento'].setValue(initialDate);
    }


    getChequesToPay() {

        let dialogRef = this.dialog.open(EeChequesPDialogComponent, {
            hasBackdrop: true
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                this.selectedCheque = {
                    chequeID: result.id,
                    numero: result.numero,
                    banco: result.banco,
                    destino: 'transferido',
                    monto: result.monto,
                    estado: 'procesado',
                    tipo: 'REC'
                };
                //this.existingChequeMonto = result.monto;
                this.transferenciaChequeForm.controls['nroCheque'].setValue(result.numero);
            }
        });

    }

    onSubmit() {
        if (!this.transferenciaChequeForm.valid) return;
        let transferenciaModel = this.transferenciaChequeForm.value;
        transferenciaModel.tipoMovimiento = 6;
        transferenciaModel.residenciaID = APPCONFIG.brand.id;
        transferenciaModel.subcategoriaID = transferenciaModel.tipo;
        transferenciaModel.cheque = this.selectedCheque;
        this.selectedCheque.monto = this.selectedCheque.monto.replace('$ ', '');
        if (this.selectedCheque.monto.indexOf('.') > -1) {
            this.selectedCheque.monto = transferenciaModel.monto.replace('.', '');
        }
        transferenciaModel.monto = this.selectedCheque.monto;

        this.eeloading.open();
        this.movimientosProvider
            .postMovimiento(transferenciaModel)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.eetoast.show('Transferencia realizada', 'toast-succeed');
                this.router.navigate(["app/dashboard"]);
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            }
            )
    }
}



