import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PeriodoProvider } from './../../../providers/periodo-provider';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { BehaviorSubject } from "rxjs/Rx";

@Component({
    selector: 'resumen-mes',
    template: require('./resumen-mes.component.html')
})
export class ResumenMesComponent {
    private isPeriodoChecked: boolean;
    private saldoTotal = 0;
    private visitaHombre = 0;
    private visitaMujer = 0;
    private visitaPareja = 0;
    private hhNecesarias = 0;
    private hhUtilizadas = 0;
    private ausentismoOperativo = 0;
    private ausentismoReal = 0;
    private horasPagas = 0;
    private props;
    private dataSource;
    private dataColumn;
    private hasRecords;
    private sourceChangesEmitter = new BehaviorSubject(null);
    resumenMesForm: FormGroup;
    constructor(
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private eetoast: EEToast,
        private errorHandler: ErrorHandler,
        private periodoProvider: PeriodoProvider,
        private movimientosProvider: MovimientosProvider) {
        this.getPeriodosDist();
    }

    ngOnInit() {
        this.resumenMesForm = this.formBuilder.group({
            periodoID: this.formBuilder.control(null, [Validators.required])
        });
        this.resumenMesForm.controls['periodoID']
            .valueChanges
            .subscribe((value) => {
                this.isPeriodoChecked = true;
                this.getMovimientosByPeriodo(value);
            });
        this.getDataColumns();
    }

    getPeriodosDist() {
        this.props = {};
        this.eeloading.open();
        this.periodoProvider
            .GetPeriodosDist(1) //APPCONFIG.brand.id
            .subscribe(
            (data: any) => {
                this.props.periodos = data;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }

    getMovimientosByPeriodo(periodo) {
        this.eeloading.open();
        this.movimientosProvider
            .GetResumenMes(periodo, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.subcategoriasData.length > 0;
                this.saldoTotal = 0;
                data.subcategoriasData.forEach(element => {
                    let dataElement = {
                        categoria: element.categoria,
                        subcategoria: element.subcategoria,
                        monto: element.monto
                    }
                    this.saldoTotal += element.monto;
                    this.dataSource.push(dataElement);
                    this.sourceChangesEmitter.next(this.dataSource);
                });
                this.visitaHombre = data.periodoData.visitaHombre;
                this.visitaMujer = data.periodoData.visitaMujer;
                this.visitaPareja = data.periodoData.visitaPareja;
                this.hhNecesarias = data.periodoData.hhNecesarias;
                this.hhUtilizadas = data.periodoData.hhUtilizadas;
                this.ausentismoOperativo = data.periodoData.ausentismoOperativo;
                this.ausentismoReal = data.periodoData.ausentismoReal;
                this.horasPagas = data.periodoData.horasPagas;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    getDataColumns() {
        this.dataColumn = [{
            key: 'categoria',
            title: 'Categoria'
        },
        {
            key: 'subcategoria',
            title: 'Subcategoria'
        },
        {
            key: 'monto',
            title: 'Monto Acum'
        }];
    }

}



