import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import { BehaviorSubject } from "rxjs/Rx";
import * as moment from 'moment'; 
var _ = require('lodash');
@Component({
    selector: 'residente-cuenta',
    template: require('./residente-cuenta.component.html'),
})
export class ResidenteCuentaComponent {
    private isResidenteChecked: boolean;
    private saldoResidente = 0;
    private props;
    private dataSourceMovimientos;
    private dataColumnMovimientos;
    private hasRecords;
    private sourceChangesEmitter = new BehaviorSubject(null);
    residenteCuentaForm: FormGroup;
    constructor(
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private movimientosProvider: MovimientosProvider) {
        this.GetResidentesByResidencia();
    }

    ngOnInit() {
        this.residenteCuentaForm = this.formBuilder.group({
            residenteID: this.formBuilder.control(null, [Validators.required])
        });
        this.residenteCuentaForm.controls['residenteID']
            .valueChanges
            .subscribe((value) => {
                this.isResidenteChecked = true;
                this.GetMovimientosByResidente(value);
            });

        this.getDataColumns();
    }

    GetResidentesByResidencia() {
        this.props = {};
        this.eeloading.open();
        this.movimientosProvider
            .getResidentesByResidencia(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                if (data && data.length > 0) {
                    data.forEach(element => {
                        element['fname'] = element.nombre.split(' ')[0];
                        element['lname'] = element.nombre.split(' ')[1];
                    });
                    data = _.orderBy(data, ['lname'], ['asc']);
                }
                this.props.residentes = data;
                this.eeloading.close();
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            })
    }

    GetMovimientosByResidente(residente) {
        this.eeloading.open();
        this.movimientosProvider
            .getResidenteCuentaByResidenteId(residente, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceMovimientos = [];
                this.hasRecords = data.detalles.length > 0;
                this.saldoResidente = data.saldo;
                let newSaldo = data.saldoInicial;
                data.detalles.forEach(element => {
                    if (element.tipo === 'cobro') {
                        newSaldo += element.monto;
                    } else {
                        newSaldo -= element.monto;
                    }
                    let dataElement = {
                        fecha: moment(element.fecha).format("DD/MM/YYYY"), 
                        detalle: element.detalle,
                        montoCobros: (element.tipo == 'cobro') ? '$ ' + element.monto : '',
                        montoGastos: (element.tipo == 'gasto') ? '($ ' + element.monto + ')' : '',
                        actual: newSaldo
                    }

                    this.dataSourceMovimientos.push(dataElement);
                    this.sourceChangesEmitter.next(this.dataSourceMovimientos);
                });

            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    getDataColumns() {
        this.dataColumnMovimientos = [
            // {
            //     key: 'id',
            //     title: 'ID'
            // },
            {
                key: 'fecha',
                title: 'Fecha'
            },
            {
                key: 'detalle',
                title: 'Detalle'
            },
            {
                key: 'montoCobros',
                title: 'Monto Cobros'
            },
            {
                key: 'montoGastos',
                title: 'Monto Gastos'
            },
            {
                key: 'actual',
                title: 'Actual'
            }];
    }

}



