import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { CuotaProvider } from './../../../providers/cuota-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    selector: 'cuota-detalle',
    template: require('./cuota-detalle.component.html'),
})
export class CuotaDetalleComponent {
    private titleForm;
    private currencyMask = EEMasks.eecurrency;
    private residenteId;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    cuotaDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    resid: number;
    constructor(
        private cuotaProvider: CuotaProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private location: Location) {
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
                this.resid = params['resid'];
            });
        this.titleForm = this.id ? 'Cuota ' : 'Agregar Cuota';
        this.buildForm();
        if (!this.id) {
            this.residenteId = this.resid;
            this.onAddForm();
        }
        else {
            this.getCuotaById();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm;
                this.onDeleteForm();
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }
        }
    }

    getCuotaById() {
        this.titleForm == "";
        this.eeloading.open();
        this.cuotaProvider
            .GetCuotaById(this.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.cuotaDetalleForm.controls['periodo'].setValue(data.periodo);
                this.cuotaDetalleForm.controls['monto'].setValue(data.monto);
                this.residenteId = data.residenteID;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.cuotaDetalleForm = this.formBuilder.group({
            periodo: this.formBuilder.control(null, [Validators.required]),
            monto: this.formBuilder.control(null, [Validators.required])
        });
    }

    enableForm() {
        if (this.modeAddForm) { this.cuotaDetalleForm.controls['periodo'].enable(); }
        else { this.cuotaDetalleForm.controls['periodo'].disable(); }
        this.cuotaDetalleForm.controls['monto'].enable();
    }

    disableForm() {
        this.cuotaDetalleForm.controls['periodo'].disable();
        this.cuotaDetalleForm.controls['monto'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onSubmit() {
        if (!this.cuotaDetalleForm.valid) return;
        let cuotaModel = this.cuotaDetalleForm.value;
        cuotaModel.monto = cuotaModel.monto.replace('$ ', '');
        if (cuotaModel.monto.indexOf('.') > -1) {
            cuotaModel.monto = cuotaModel.monto.replace('.', '');
        }
        cuotaModel.residenteID = this.residenteId;
        cuotaModel.Id = this.id;
        this.eeloading.open();
        if (this.modeAddForm) {
            this.cuotaProvider
                .PostCuota(cuotaModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Cuota agregada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
        else if (this.modeEditForm) {
            this.cuotaProvider
                .PutCuota(cuotaModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Cuota actualizada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )

        }
    }

    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.cuotaProvider
                .DeleteCuota(this.id)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Cuota borrada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



