import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { FamiliarProvider } from './../../../providers/familiar-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    selector: 'familiar-detalle',
    template: require('./familiar-detalle.component.html'),
})
export class FamiliarDetalleComponent {
    private titleForm;
    private residenteId;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    familiarDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    resid: number;
    constructor(
        private familiarProvider: FamiliarProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private location: Location) {
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
                this.resid = params['resid'];
            });
        this.titleForm = this.id ? 'Familiar ' : 'Agregar Familiar';
        this.buildForm();
        if (!this.id) {
            this.residenteId = this.resid;
            this.onAddForm();
        }
        else {
            this.getFamiliarById();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm;
                this.onDeleteForm();
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }
        }
    }

    getFamiliarById() {
        this.titleForm == "";
        this.eeloading.open();
        this.familiarProvider
            .GetFamiliarById(this.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                // this.familiarDetalleForm.controls['familiarID'].setValue(data.familiarID);
                // this.familiarDetalleForm.controls['residenteID'].setValue(data.residenteID);
                this.familiarDetalleForm.controls['nombre'].setValue(data.nombre);
                this.familiarDetalleForm.controls['relacion'].setValue(data.relacion);
                this.familiarDetalleForm.controls['direccion'].setValue(data.direccion);
                this.familiarDetalleForm.controls['localidad'].setValue(data.localidad);
                this.familiarDetalleForm.controls['telefono'].setValue(data.telefono);
                this.familiarDetalleForm.controls['email'].setValue(data.email);
                this.residenteId = data.residenteID;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.familiarDetalleForm = this.formBuilder.group({
            // familiarID: this.formBuilder.control(null, [Validators.required]),
            // residenteID: this.formBuilder.control(null, [Validators.required]),
            nombre: this.formBuilder.control(null, [Validators.required]),
            relacion: this.formBuilder.control(null, [Validators.required]),
            direccion: this.formBuilder.control(null),
            localidad: this.formBuilder.control(null),
            telefono: this.formBuilder.control(null),
            email: this.formBuilder.control(null),
        });
    }

    enableForm() {
        // this.familiarDetalleForm.controls['familiarID'].disable();
        // this.familiarDetalleForm.controls['residenteID'].disable();
        this.familiarDetalleForm.controls['nombre'].enable();
        this.familiarDetalleForm.controls['relacion'].enable();
        this.familiarDetalleForm.controls['direccion'].enable();
        this.familiarDetalleForm.controls['localidad'].enable();
        this.familiarDetalleForm.controls['telefono'].enable();
        this.familiarDetalleForm.controls['email'].enable();
    }

    disableForm() {
        // this.familiarDetalleForm.controls['familiarID'].disable();
        // this.familiarDetalleForm.controls['residenteID'].disable();
        this.familiarDetalleForm.controls['nombre'].disable();
        this.familiarDetalleForm.controls['relacion'].disable();
        this.familiarDetalleForm.controls['direccion'].disable();
        this.familiarDetalleForm.controls['localidad'].disable();
        this.familiarDetalleForm.controls['telefono'].disable();
        this.familiarDetalleForm.controls['email'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onSubmit() {
        if (!this.familiarDetalleForm.valid) return;
        let familiarModel = this.familiarDetalleForm.value;
        familiarModel.residenteID = this.residenteId;
        familiarModel.familiarID = this.id;
        this.eeloading.open();
        if (this.modeAddForm) {
            this.familiarProvider
                .PostFamiliar(familiarModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Familiar agregado', 'toast-succeed');
                    //this.router.navigate(["app/dashboard"]);
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
        else if (this.modeEditForm) {
            this.familiarProvider
                .PutFamiliar(familiarModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Familiar actualizado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )

        }
    }

    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.familiarProvider
                .DeleteFamiliar(this.id)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Familiar borrado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



