import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PersonalProvider } from './../../../providers/personal-provider';
import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'personales',
    template: require('./personales.component.html'),
})
export class PersonalesComponent {
    private hasRecords;
    private dataSource;
    private dataSet;
    private dataColumn;
    constructor(
        private router: Router,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eeToast: EEToast,
        private personalProvider: PersonalProvider,
        private dataStg: DataStorageProvider) {
            this.getPersonales();
        }

    ngOnInit() {
        this.dataColumn = [                             
            {
                key: 'nombre',
                title: 'Nombre'
            },
            {
                key: 'legajo',
                title: 'Legajo'
            },
            {
                key: 'UserName',
                title: 'User'
            },
            {
                key: 'puesto',
                title: 'Puesto'
            },
            {
                key: 'activo',
                title: 'Activo'
            }];
    }

    getPersonales() {
        this.eeloading.open();
        this.personalProvider
            .GetPersonalesAll(APPCONFIG.brand.id)
            .subscribe(
            data => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.length > 0;
                data.forEach(element => {
                    let dataElement = {
                        personalID: element.personalID,
                        nombre: element.nombre,
                        legajo: element.numeroLegajo,
                        UserName: element.UserName,
                        puesto: element.puesto.nombre,
                        activo: (element.activo) ? 'Si' : 'No'
                    }
                    this.dataSource.push(dataElement);
                });
                
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    onAddPersonal() {
        this.router.navigate(['/app/personal-detalle']);
    }

    onEditPersonal(obj) {
        this.router.navigate(['/app/personal-detalle'], {queryParams: {id: obj.personalID}});
    }

    onDeletePersonal(obj) {
        this.router.navigate(['/app/personal-detalle'], {queryParams: {id: obj.personalID, action: 'd'}});
    }

}



