import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { ResidenteProvider } from './../../../providers/residente-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Router } from '@angular/router';

@Component({
    selector: 'residente-detalle',
    template: require('./residente-detalle.component.html'),
})
export class ResidenteDetalleComponent {
    private titleForm;
    private props;
    private medioPagoNombre;
    private residenciaMatrizNombre;
    private causaSalidaNombre;
    private hasRecordsCuotas;
    private dataSourceCuotas;
    private dataSetCuotas;
    private dataColumnCuotas;
    private hasRecordsFamiliares;
    private dataSourceFamiliares;
    private dataSetFamiliares;
    private dataColumnFamiliares;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    residenteDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    status: string;
    constructor(
        private residenteProvider: ResidenteProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eetoast: EEToast) {
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
                this.status = params['status'];
            });
        this.titleForm = this.id ? 'Residente ' : 'Agregar Residente';
        this.buildForm();
        if (!this.id) {
            this.getResidenteDetalleAdd();
            this.onAddForm();
        }
        else {
            this.getResidenteDetalle();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm;
                this.onDeleteForm();
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }
        }
        this.dataColumnCuotas = [
            {
                key: 'periodo',
                title: 'Periodo'
            },
            {
                key: 'monto',
                title: 'Monto'
            }];
        this.dataColumnFamiliares = [
            {
                key: 'nombre',
                title: 'Nombre'
            },
            {
                key: 'relacion',
                title: 'Relacion'
            },
            {
                key: 'email',
                title: 'Email'
            },
            {
                key: 'telefono',
                title: 'Telefono'
            }];
    }

    getResidenteDetalleAdd() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.residenteProvider
            .GetResidenteDetalle(0, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.props = data.data;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    getResidenteDetalle() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.residenteProvider
            .GetResidenteDetalle(this.id, APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                data.residente.residenciaMatrizID = (data.residente.activo) ? data.residente.residenciaMatrizID : null;
                this.residenteDetalleForm.controls['nombre'].setValue(data.residente.nombre);
                this.residenteDetalleForm.controls['fechaNacimiento'].setValue(data.residente.fechaNacimiento);
                this.residenteDetalleForm.controls['tipoDocumento'].setValue(data.residente.tipoDocumento);
                this.residenteDetalleForm.controls['numeroDocumento'].setValue(data.residente.numeroDocumento);
                this.residenteDetalleForm.controls['obraSocial'].setValue(data.residente.obraSocial);
                this.residenteDetalleForm.controls['clasificacionPaciente'].setValue(data.residente.clasificacionPaciente);
                this.residenteDetalleForm.controls['patologia'].setValue(data.residente.patologia);
                this.residenteDetalleForm.controls['fechaIngreso'].setValue(data.residente.fechaIngreso);
                this.residenteDetalleForm.controls['cuotaInicial'].setValue(data.residente.cuotaInicial);
                this.residenteDetalleForm.controls['saldoInicial'].setValue(data.residente.saldoInicial);
                this.residenteDetalleForm.controls['grupoAumento'].setValue(data.residente.grupoAumento);
                this.residenteDetalleForm.controls['diaPago'].setValue(data.residente.diaPago);
                this.residenteDetalleForm.controls['medioPagoID'].setValue(data.residente.medioPagoID);
                this.residenteDetalleForm.controls['fechaSalida'].setValue(data.fechaSalida);
                this.residenteDetalleForm.controls['causaSalidaID'].setValue(data.residente.causaSalidaID);
                this.residenteDetalleForm.controls['residenciaMatrizID'].setValue(data.residente.residenciaMatrizID);
                this.residenteDetalleForm.controls['activo'].setValue(data.residente.activo);
                this.medioPagoNombre = data.residente.medioPago.nombre;
                this.residenciaMatrizNombre = data.residente.residenciaMatriz.residenciaMatrizID;
                this.causaSalidaNombre = (data.residente.causaSalidaID == null) ? '' : data.residente.causaSalida.nombre;
                this.props = data.data;
                this.dataSourceCuotas = [];
                this.hasRecordsCuotas = data.residente.cuotas.length > 0;
                data.residente.cuotas.forEach(element => {
                    let dataElement = {
                        id: element.Id,
                        periodo: element.periodo,
                        monto: element.monto
                    }
                    this.dataSourceCuotas.push(dataElement);
                });
                this.dataSourceFamiliares = [];
                this.hasRecordsFamiliares = data.residente.familiares.length > 0;
                data.residente.familiares.forEach(element => {
                    let dataElement = {
                        familiarID: element.familiarID,
                        nombre: element.nombre,
                        relacion: element.relacion,
                        email: element.email,
                        telefono: element.telefono
                    }
                    this.dataSourceFamiliares.push(dataElement);
                });
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.residenteDetalleForm = this.formBuilder.group({
            nombre: this.formBuilder.control(null, [Validators.required]),
            fechaNacimiento: this.formBuilder.control(null, [Validators.required]),
            tipoDocumento: this.formBuilder.control(null, [Validators.required]),
            numeroDocumento: this.formBuilder.control(null, [Validators.required]),
            obraSocial: this.formBuilder.control(null, [Validators.required]),
            clasificacionPaciente: this.formBuilder.control(null, [Validators.required]),
            patologia: this.formBuilder.control(null),
            fechaIngreso: this.formBuilder.control(null, [Validators.required]),
            cuotaInicial: this.formBuilder.control(null, [Validators.required]),
            saldoInicial: this.formBuilder.control(null, [Validators.required]),
            grupoAumento: this.formBuilder.control(null, [Validators.required]),
            diaPago: this.formBuilder.control(null, [Validators.required]),
            medioPagoID: this.formBuilder.control(null, [Validators.required]),
            fechaSalida: this.formBuilder.control(null),
            causaSalidaID: this.formBuilder.control(null),
            residenciaMatrizID: this.formBuilder.control(null, [Validators.required]),
            activo: this.formBuilder.control(null),
        });
        let initialDate = new Date();
        this.residenteDetalleForm.controls['fechaIngreso'].setValue(initialDate);
        this.residenteDetalleForm.controls['activo'].setValue(true);
        this.residenteDetalleForm.controls['saldoInicial'].setValue(0);
    }

    enableForm() {
        this.residenteDetalleForm.controls['nombre'].enable();
        this.residenteDetalleForm.controls['fechaNacimiento'].enable();
        this.residenteDetalleForm.controls['tipoDocumento'].enable();
        this.residenteDetalleForm.controls['numeroDocumento'].enable();
        this.residenteDetalleForm.controls['obraSocial'].enable();
        this.residenteDetalleForm.controls['clasificacionPaciente'].enable();
        this.residenteDetalleForm.controls['patologia'].enable();
        this.residenteDetalleForm.controls['fechaIngreso'].enable();
        this.residenteDetalleForm.controls['cuotaInicial'].enable();
        this.residenteDetalleForm.controls['saldoInicial'].enable();
        this.residenteDetalleForm.controls['grupoAumento'].enable();
        this.residenteDetalleForm.controls['diaPago'].enable();
        this.residenteDetalleForm.controls['medioPagoID'].enable();
        this.residenteDetalleForm.controls['fechaSalida'].enable();
        this.residenteDetalleForm.controls['causaSalidaID'].enable();
        this.residenteDetalleForm.controls['residenciaMatrizID'].enable();
        this.residenteDetalleForm.controls['activo'].enable();
    }

    disableForm() {
        this.residenteDetalleForm.controls['nombre'].disable();
        this.residenteDetalleForm.controls['fechaNacimiento'].disable();
        this.residenteDetalleForm.controls['tipoDocumento'].disable();
        this.residenteDetalleForm.controls['numeroDocumento'].disable();
        this.residenteDetalleForm.controls['obraSocial'].disable();
        this.residenteDetalleForm.controls['clasificacionPaciente'].disable();
        this.residenteDetalleForm.controls['patologia'].disable();
        this.residenteDetalleForm.controls['fechaIngreso'].disable();
        this.residenteDetalleForm.controls['cuotaInicial'].disable();
        this.residenteDetalleForm.controls['saldoInicial'].disable();
        this.residenteDetalleForm.controls['grupoAumento'].disable();
        this.residenteDetalleForm.controls['diaPago'].disable();
        this.residenteDetalleForm.controls['medioPagoID'].disable();
        this.residenteDetalleForm.controls['fechaSalida'].disable();
        this.residenteDetalleForm.controls['causaSalidaID'].disable();
        this.residenteDetalleForm.controls['residenciaMatrizID'].disable();
        this.residenteDetalleForm.controls['activo'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onAddCuota() {
        this.router.navigate(['/app/cuota-detalle'], { queryParams: { resid: this.id } });
    }

    onEditCuota(obj) {
        this.router.navigate(['/app/cuota-detalle'], { queryParams: { id: obj.id } });
    }

    onDeleteCuota(obj) {
        this.router.navigate(['/app/cuota-detalle'], { queryParams: { id: obj.id, action: 'd' } });
    }

    onAddFamiliar() {
        this.router.navigate(['/app/familiar-detalle'], { queryParams: { resid: this.id } });
    }

    onEditFamiliar(obj) {
        this.router.navigate(['/app/familiar-detalle'], { queryParams: { id: obj.familiarID } });
    }

    onDeleteFamiliar(obj) {
        this.router.navigate(['/app/familiar-detalle'], { queryParams: { id: obj.familiarID, action: 'd' } });
    }

    onOk() {
        if (!this.residenteDetalleForm.valid) return;
        let residenteModel = this.residenteDetalleForm.value;
        this.eeloading.open();
        if (this.modeAddForm) {
            residenteModel.residenciaID = APPCONFIG.brand.id;
            this.residenteProvider
                .PostResidente(residenteModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Residente agregado', 'toast-succeed');
                    this.router.navigate(['/app/residentes'], { queryParams: { status: this.status } });
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )
        }
        else if (this.modeEditForm) {
            residenteModel.residenteID = this.id;
            this.residenteProvider
                .PutResidente(residenteModel)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Residente actualizado', 'toast-succeed');
                    this.router.navigate(['/app/residentes'], { queryParams: { status: this.status } });
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )

        }
    }

    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.residenteProvider
                .DeleteResidente(this.id)
                .subscribe(
                (data: any) => {
                    this.eeloading.close();
                    this.eetoast.show('Residente borrado', 'toast-succeed');
                    this.router.navigate(['/app/residentes'], { queryParams: { status: this.status } });
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )
        }
    }

    onCancel() {
        this.router.navigate(['/app/residentes'], { queryParams: { status: this.status } });
    }

}



