import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PersonalResidenciaProvider } from './../../../providers/personal-residencia-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

@Component({
    selector: 'personal-residencia-detalle',
    template: require('./personal-residencia-detalle.component.html'),
})
export class PersonalResidenciaDetalleComponent {
    private titleForm;
    private props;
    private residenciaNombre;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    personalResidenciaDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    persid: number;
    constructor(
        private personalResidenciaProvider: PersonalResidenciaProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast,
        private location: Location) {
    }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
                this.persid = params['persid'];
            });
        this.titleForm = this.id ? 'Personal Residencia ' : 'Agregar Personal Residencia';
        this.buildForm();
        if (!this.id) { 
            this.getPersonalResidenciaDetalleAdd();
            this.onAddForm(); 
        } 
        else { 
            this.getPersonalResidenciaDetalle();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm; 
                this.onDeleteForm(); 
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }            
        }
    }

    getPersonalResidenciaDetalleAdd() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.personalResidenciaProvider
            .GetPersonalResidenciaDetalle(0, this.persid)
            .subscribe(
            data => {
                this.eeloading.close();
                this.props = data.data;
                if (data.data.residencias.length == 0) {
                    this.eetoast.show('Advertencia, Personal ya habilitado en todas las residencias.', 'toast-warning')
                };
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    
    getPersonalResidenciaDetalle() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.personalResidenciaProvider
            .GetPersonalResidenciaDetalle(this.id, this.persid)
            .subscribe(
            data => {
                this.eeloading.close();
                this.personalResidenciaDetalleForm.controls['residenciaID'].setValue(data.personalResidencia.residenciaID);
                this.residenciaNombre = data.personalResidencia.residencia.nombre;
                //this.residenciaID = data.personalResidencias.residenciaID;
                this.props = data.data;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.personalResidenciaDetalleForm = this.formBuilder.group({
            residenciaID: this.formBuilder.control(null, [Validators.required]),
        });
    }

    enableForm() {
        this.personalResidenciaDetalleForm.controls['residenciaID'].enable();
    }

    disableForm() {
        this.personalResidenciaDetalleForm.controls['residenciaID'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onSubmit() {
        if (!this.personalResidenciaDetalleForm.valid) return;
        let personalResidenciaModel = this.personalResidenciaDetalleForm.value;
        
        this.eeloading.open();
        if (this.modeAddForm) {
            personalResidenciaModel.personalID = this.persid;
            this.personalResidenciaProvider
                .PostPersonalResidencia(personalResidenciaModel)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal Residencia agregada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
        else if (this.modeEditForm) {
            personalResidenciaModel.personalResidenciaID = this.id;
            this.personalResidenciaProvider
                .PutPersonalResidencia(personalResidenciaModel)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal Residencia actualizada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )

        }
    }

    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.personalResidenciaProvider
                .DeletePersonalResidencia(this.id)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal Residencia  borrada', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.errorHandler.handleError(error);
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



