import { EEGerCommons } from './../../../utils/ee-ger.common';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component } from '@angular/core';
import * as moment from 'moment';
var _ = require('lodash');
@Component({
    selector: 'composicion-caja',
    template: require('./composicion-caja.component.html'),
})
export class ComposicionCajaComponent {
    public AppConfig: any;
    private nombreCuenta;
    private saldoCuenta = 0;
    private props;
    private dataSourcePendientes;
    private dataSourceMovimientos;
    private dataColumnPendientes;
    private dataColumnMovimientos;
    resumenCuentaForm: FormGroup;
    formSubmitted = false;
    constructor(
        private movimientosProvider: MovimientosProvider,
        private formBuilder: FormBuilder,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eetoast: EEToast) {
        this.eeloading.open();
        this.getComposicionCaja();
    }

    ngOnInit() {
        this.AppConfig = APPCONFIG;
        this.dataColumnMovimientos = [
            {
                key: 'fecha',
                title: 'Fecha'
            },
            {
                key: 'banco',
                title: 'Banco'
            },
            {
                key: 'aPagar',
                title: 'A Pagar'
            },
            {
                key: 'disponible',
                title: 'Disponible'
            },
            {
                key: 'efectivo',
                title: 'Efectivo Total'
            },
            {
                key: 'cheques',
                title: 'Cheques'
            },
            {
                key: 'total',
                title: 'Total'
            }];

    }

    getComposicionCaja() {
        this.eeloading.open();
        this.movimientosProvider
            .GetComposicionCaja(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceMovimientos = [];
                this.dataSourcePendientes = [];

                let totalBancoActual = 0;
                let aPagarActual = 0;
                let efectivoActual = 0;
                let chequesACuentaActual = 0;
                let disponibleActual = 0;
                data.forEach(element => {
                    totalBancoActual = totalBancoActual + (element.activoBanco - element.pasivoBanco);
                    aPagarActual = aPagarActual + element.aPagarCheques;
                    efectivoActual = efectivoActual + (element.activoEfectivo - element.pasivoEfectivo);
                    chequesACuentaActual = chequesACuentaActual + element.chequesACuenta - element.chequesACuentaCancelados;
                    disponibleActual = totalBancoActual - aPagarActual
                    let dataElement = {
                        fecha: moment(element.fechaEmision).format("DD/MM/YYYY"),
                        banco: totalBancoActual,
                        aPagar: aPagarActual,
                        disponible: disponibleActual,
                        efectivo: efectivoActual,
                        cheques: chequesACuentaActual,
                        total: efectivoActual + disponibleActual + chequesACuentaActual
                    }
                    this.dataSourceMovimientos.push(dataElement)
                });
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
}


