import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { ChequesProvider } from './../../../providers/cheques-provider';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { Component } from '@angular/core';
import { Router } from "@angular/router";
import { EEGerCommons } from './../../../utils/ee-ger.common';
var _ = require('lodash');
@Component({
    selector: 'cheques',
    template: require('./cheques.component.html')
})
export class ChequesComponent {
    dataSourceRecibidos;
    hasRecords;
    today;
    dataColumnRecibidos;
    constructor(
        private movimientoProvider: MovimientosProvider,
        private chequesProvider: ChequesProvider,
        private router: Router,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eeToast: EEToast,
        private dataStg: DataStorageProvider) {
        this.eeloading.open();
        this.today = new Date();
        let _residenciaID = APPCONFIG.brand.id || this.dataStg.getValue('residencia');
        this.movimientoProvider.getMovimientosByMedio(3, _residenciaID, 'REC')
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSourceRecibidos = [];
                this.hasRecords = data.length > 0;
                data = _.uniqBy(data, function (e) {
                    return e.chequeID;
                });
                data.forEach(element => {
                    let dataElement = {
                        fecha: element.fechaMovimiento ? EEGerCommons.formattedDate(element.fechaMovimiento) : '',
                        chequeID: element.cheque.chequeID,
                        fechaCobro: element.cheque.fechaVencimiento ? EEGerCommons.formattedDate(element.cheque.fechaVencimiento) : '',
                        fechaEntrega: element.cheque.fechaEntrega ? EEGerCommons.formattedDate(element.cheque.fechaEntrega) : '',
                        fechaVencimiento: element.cheque.fechaVencimiento ? EEGerCommons.sum30Days(element.cheque.fechaVencimiento) : '',
                        tipo: element.cheque.tipo,
                        monto: '$ ' + element.monto,
                        numero: element.cheque.numero,
                        banco: element.cheque.banco,
                        origen: element.residente ? element.residente.nombre : '',
                        destino: element.cheque.destino,
                        cubierto: Boolean(element.cheque.estado === 'procesado'),
                    };
                    this.dataSourceRecibidos.push(dataElement);

                });
                this.dataSourceRecibidos = _.orderBy(this.dataSourceRecibidos, ['fechaEntrega'], ['asc']);
                this.dataColumnRecibidos = [{
                    key: 'fecha',
                    title: 'Fecha'
                },
                {
                    key: 'fechaCobro',
                    title: 'Fecha Cobro'
                },
                {
                    key: 'fechaVencimiento',
                    title: 'Fecha Vto'
                },
                {
                    key: 'fechaEntrega',
                    title: 'Fecha Entrega'
                },
                {
                    key: 'monto',
                    title: 'Monto'
                },
                {
                    key: 'numero',
                    title: 'Numero'
                },
                {
                    key: 'banco',
                    title: 'Banco'
                },
                {
                    key: 'origen',
                    title: 'Residente'
                },
                {
                    key: 'destino',
                    title: 'Destino'
                }]
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
}



