import { ErrorHandler } from './../../../handlers/errorHandler';
import { ChildTable } from './../../../components/common/ee-table/ee-table.component';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { Component } from '@angular/core';

@Component({
    selector: 'flujofondos',
    template: require('./flujofondos.component.html')
})
export class FlujofondosComponent {
    dataSource;
    dataSet;
    dataColumn;
    childTable: ChildTable;
    constructor(
        private movimientoProvider: MovimientosProvider,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eeToast: EEToast) {
        this.eeloading.open();
        this.movimientoProvider.getMovimientosFlujoFondos(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                console.log(data);
                data.forEach(element => {
                    let dataElement = {
                        categoriaID: element.categoriaID,
                        nombre: element.nombre,
                        total: element.total
                    }
                    this.dataSource.push(dataElement);
                });
                this.dataColumn = [{
                    key: 'categoriaID',
                    title: 'ID'
                },
                {
                    key: 'nombre',
                    title: 'Categoría'
                },
                {
                    key: 'total',
                    title: 'TOTAL'
                }];
                this.childTable = {
                    hideHeader: false,
                    trackBy: 'categoriaID',
                    columnDefs: [
                        { key: 'subcategoriaID', title: 'ID' },
                        { key: 'nombre', title: 'Subcategoria' },
                        { key: 'total', title: 'Total' }
                    ],
                    dataSet: data,
                    hasDetail: true,
                    detailProperty: 'movimientos',
                    sourceProperty: 'subcategorias',
                    hideFilter: true
                }
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    selectedMovimiento(event) {
        console.log("eve", event);
    }
}



