import { ErrorHandler } from './../../../handlers/errorHandler';
import { MatDialog } from '@angular/material';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { MovimientosProvider } from './../../../providers/movimientos-provider';
import { Component } from '@angular/core';
import { ChildTable } from './../../../components/common/ee-table/ee-table.component';
import { EeFacturacionDialogComponent } from './../../../components/common/ee-facturacion-dialog/ee-facturacion-dialog.component';
import * as moment from 'moment';
@Component({
    selector: 'facturacion-mensual',
    template: require('./facturacion-mensual.component.html'),
})
export class FacturacionMensualComponent {
    private dataSource;
    private periodo;
    private totDeuda = 0;
    private totCobro = 0;
    private totSaldo = 0;
    private dataSet;
    private dataColumn;
    private childTable: ChildTable;
    constructor(
        private movimientosProvider: MovimientosProvider,
        private dialog: MatDialog,
        private errorHandler: ErrorHandler,
        private eeloading: EELoading,
        private eetoast: EEToast) {
        this.eeloading.open();
        this.getMovimientosFacturacionMensual();
    }

    ngOnInit() {
        this.periodo = moment(new Date()).format("MMYYYY");
        this.dataColumn = [{
            key: 'habitacion',
            title: 'Habit'
        },
        {
            key: 'cama',
            title: 'Cama'
        },
        {
            key: 'nombre',
            title: 'Residente'
        },
        {
            key: 'grupoAumento',
            title: 'Gr.Aum'
        },
        {
            key: 'diaPago',
            title: 'Día P'
        },
        {
            key: 'medioPago',
            title: 'M.Pago'
        },
        {
            key: 'saldoInicial',
            title: 'Saldo Ant.(S)'
        },
        {
            key: 'cuota',
            title: 'Precio(P)'
        },
        {
            key: 'extras',
            title: 'Extras(E)'
        },
        {
            key: 'total',
            title: 'Total(S+P+E)'
        },
        {
            key: 'cobros',
            title: 'Cobros'
        },
        {
            key: 'saldo',
            title: 'Saldo'
        }];
    }

    getMovimientosFacturacionMensual() {
        this.eeloading.open();
        this.movimientosProvider.getMovimientosFacturacionMensual(APPCONFIG.brand.id)
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                data.forEach(element => {
                    let dataElement = {
                        residenciaMatrizID: element.residenciaMatriz.residenciaMatrizID,
                        habitacion: element.residenciaMatriz.habitacion,
                        cama: element.residenciaMatriz.cama,
                        nombre: (element.residente === null) ? '' : (element.residente.nombre).substring(0, 20),
                        grupoAumento: (element.residente === null) ? '' : element.residente.grupoAumento,
                        diaPago: (element.residente === null) ? '' : element.residente.diaPago,
                        medioPago: (element.residente === null) ? '' : (element.residente.medioPago.nombre).substring(0, 2),
                        cuota: (element.residente === null) ? '' : element.cuota, 
                        saldoInicial: element.saldoInicial * (-1),
                        extras: element.extras,
                        total: (element.saldoInicial * (-1)) + ((element.residente === null) ? 0 : element.cuota) + element.extras,
                        cobros: element.pagos,
                        cargos: element.cargos,
                        servicios: element.servicios,
                        saldo: element.saldoInicial + element.pagos - (((element.residente === null) ? 0 : element.cuota) + element.extras)
                    }
                    this.totDeuda += dataElement.total;
                    this.totCobro += dataElement.cobros;
                    this.totSaldo += dataElement.saldo;
                    this.dataSource.push(dataElement);
                });
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    retrievedOne(row) {
        //console.log("data", row);
        let dialogRef = this.dialog.open(EeFacturacionDialogComponent, {
            hasBackdrop: true,
            data: row
        });
    }
}



