import { ErrorHandler } from './../../../handlers/errorHandler';
import { element } from 'protractor';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { PersonalProvider } from './../../../providers/personal-provider';
import { FormGroup, FormBuilder, Validators, FormControl, FormGroupDirective } from '@angular/forms';
import { EEMasks } from './../../../utils/eemasks';
import { Component, enableProdMode, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Router } from '@angular/router';

@Component({
    selector: 'personal-detalle',
    template: require('./personal-detalle.component.html'),
})
export class PersonalDetalleComponent {
    private titleForm;
    private props;
    private puestoNombre;
    private hasRecordsPersonalResidencias;
    private dataSourcePersonalResidencias;
    private dataSetPersonalResidencias;
    private dataColumnPersonalResidencias;
    private modeAddForm;
    private modeEditForm;
    private modeDeleteForm;
    personalDetalleForm: FormGroup;
    formSubmitted = false;
    id: number;
    action: any;
    constructor(
        private personalProvider: PersonalProvider,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private errorHandler: ErrorHandler,
        private location: Location,
        private eeloading: EELoading,
        private eetoast: EEToast) {
        }

    ngOnInit() {
        this.route
            .queryParams
            .subscribe(params => {
                this.id = params['id'];
                this.action = params['action'];
            });
        this.titleForm = this.id ? 'Personal ' : 'Agregar Personal';
        this.buildForm();
        if (!this.id) { 
            this.getPersonalDetalleAdd();
            this.onAddForm(); 
        } 
        else { 
            this.getPersonalDetalle();
            if (this.action == 'd') {
                this.titleForm = 'Borrar ' + this.titleForm; 
                this.onDeleteForm(); 
            }
            else {
                this.titleForm = 'Editar ' + this.titleForm;
                this.onEditForm();
            }            
        }
        this.dataColumnPersonalResidencias = [
        // {
        //     key: 'id',
        //     title: 'Id'
        // },
        // {
        //     key: 'residenciaID',
        //     title: 'residenciaID'
        // },
        {
            key: 'nombre',
            title: 'Nombre'
        }];
    }

    getPersonalDetalleAdd() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.personalProvider
            .GetPersonalDetalle(0, APPCONFIG.brand.id)
            .subscribe(
            data => {
                this.eeloading.close();
                this.props = data.data;
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }
    
    getPersonalDetalle() {
        this.props = {};
        this.buildForm();
        this.eeloading.open();
        this.personalProvider
            .GetPersonalDetalle(this.id, APPCONFIG.brand.id)
            .subscribe(
            data => {
                this.eeloading.close();
                this.personalDetalleForm.controls['UserName'].setValue(data.personal.UserName);
                this.personalDetalleForm.controls['nombre'].setValue(data.personal.nombre);
                this.personalDetalleForm.controls['fechaNacimiento'].setValue(data.personal.fechaNacimiento);
                this.personalDetalleForm.controls['tipoDocumento'].setValue(data.personal.tipoDocumento);
                this.personalDetalleForm.controls['numeroDocumento'].setValue(data.personal.numeroDocumento);
                this.personalDetalleForm.controls['numeroLegajo'].setValue(data.personal.numeroLegajo);
                this.personalDetalleForm.controls['fechaAlta'].setValue(data.personal.fechaAlta);
                this.personalDetalleForm.controls['puestoID'].setValue(data.personal.puestoID);
                this.personalDetalleForm.controls['sueldoBasico'].setValue(data.personal.sueldoBasico);
                this.personalDetalleForm.controls['horasCupo'].setValue(data.personal.horasCupo);
                this.personalDetalleForm.controls['fechaBaja'].setValue(data.fechaBaja);
                this.personalDetalleForm.controls['causaSalida'].setValue(data.personal.causaSalida);
                this.personalDetalleForm.controls['activo'].setValue(data.personal.activo);
                this.puestoNombre = data.personal.puesto.nombre;
                this.props = data.data;
                this.dataSourcePersonalResidencias = [];
                this.hasRecordsPersonalResidencias = data.data.personalResidencias.length > 0;
                data.data.personalResidencias.forEach(element => {
                    let dataElement = {
                        id: element.Id,
                        residenciaID: element.residenciaID,
                        nombre: element.residencia.nombre
                    }
                    this.dataSourcePersonalResidencias.push(dataElement);
                });
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });
    }

    buildForm() {
        this.personalDetalleForm = this.formBuilder.group({
            UserName: this.formBuilder.control(null, [Validators.required]),
            nombre: this.formBuilder.control(null, [Validators.required]),
            fechaNacimiento: this.formBuilder.control(null, [Validators.required]),
            tipoDocumento: this.formBuilder.control(null, [Validators.required]),
            numeroDocumento: this.formBuilder.control(null, [Validators.required]),
            numeroLegajo: this.formBuilder.control(null, [Validators.required]),
            fechaAlta: this.formBuilder.control(null, [Validators.required]),
            puestoID: this.formBuilder.control(null, [Validators.required]),
            sueldoBasico: this.formBuilder.control(null, [Validators.required]),
            horasCupo: this.formBuilder.control(null),
            fechaBaja: this.formBuilder.control(null),
            causaSalida: this.formBuilder.control(null),
            activo: this.formBuilder.control(null),
        });
        let initialDate = new Date();
        this.personalDetalleForm.controls['fechaAlta'].setValue(initialDate);
        this.personalDetalleForm.controls['activo'].setValue(true);
        this.personalDetalleForm.controls['sueldoBasico'].setValue(0);
        this.personalDetalleForm.controls['horasCupo'].setValue(0);
    }

    enableForm() {
        this.personalDetalleForm.controls['UserName'].enable();
        this.personalDetalleForm.controls['nombre'].enable();
        this.personalDetalleForm.controls['fechaNacimiento'].enable();
        this.personalDetalleForm.controls['tipoDocumento'].enable();
        this.personalDetalleForm.controls['numeroDocumento'].enable();
        this.personalDetalleForm.controls['numeroLegajo'].enable();
        this.personalDetalleForm.controls['fechaAlta'].enable();
        //this.personalDetalleForm.controls['puestoID'].enable();
        this.personalDetalleForm.controls['sueldoBasico'].enable();
        this.personalDetalleForm.controls['horasCupo'].enable();
        this.personalDetalleForm.controls['fechaBaja'].enable();
        this.personalDetalleForm.controls['causaSalida'].enable();
        this.personalDetalleForm.controls['activo'].enable();
    }

    disableForm() {
        this.personalDetalleForm.controls['UserName'].disable();
        this.personalDetalleForm.controls['nombre'].disable();
        this.personalDetalleForm.controls['fechaNacimiento'].disable();
        this.personalDetalleForm.controls['tipoDocumento'].disable();
        this.personalDetalleForm.controls['numeroDocumento'].disable();
        this.personalDetalleForm.controls['numeroLegajo'].disable();
        this.personalDetalleForm.controls['fechaAlta'].disable();
        //this.personalDetalleForm.controls['puestoID'].disable();
        this.personalDetalleForm.controls['sueldoBasico'].disable();
        this.personalDetalleForm.controls['horasCupo'].disable();
        this.personalDetalleForm.controls['fechaBaja'].disable();
        this.personalDetalleForm.controls['causaSalida'].disable();
        this.personalDetalleForm.controls['activo'].disable();
    }

    onAddForm() {
        this.modeAddForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onEditForm() {
        this.modeEditForm = true;
        this.enableForm();
        this.formSubmitted = false;
    }

    onDeleteForm() {
        this.modeDeleteForm = true;
        this.disableForm();
        this.formSubmitted = false;
    }

    onAddPersonalResidencia() {
        this.router.navigate(['/app/personal-residencia-detalle'], {queryParams: {persid: this.id}});
    }

    onEditPersonalResidencia(obj) {
        this.router.navigate(['/app/personal-residencia-detalle'], {queryParams: {id: obj.id}});
    }

    onDeletePersonalResidencia(obj) {
        this.router.navigate(['/app/personal-residencia-detalle'], {queryParams: {id: obj.id, action: 'd', persid: 0}});
    }

    onOk() {
        if (!this.personalDetalleForm.valid) return;
        let personalModel = this.personalDetalleForm.value;
        this.eeloading.open();
        if (this.modeAddForm) {
            personalModel.residenciaID = APPCONFIG.brand.id;
            this.personalProvider
                .PostPersonal(personalModel, APPCONFIG.brand.id)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal agregado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )
        }
        else if (this.modeEditForm) {
            personalModel.personalID = this.id;
            this.personalProvider
                .PutPersonal(personalModel)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal actualizado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )

        }
    }

    onDelete() {
        this.eeloading.open();
        if (this.modeDeleteForm) {
            this.personalProvider
                .DeletePersonal(this.id)
                .subscribe(
                data => {
                    this.eeloading.close();
                    this.eetoast.show('Personal borrado', 'toast-succeed');
                    this.location.back();
                },
                error => {
                    this.eeloading.close();
                    this.eetoast.show('Error, intente nuevamente', 'toast-error');
                }
                )
        }
    }

    onCancel() {
        this.location.back();
    }

}



